import { Dispatch, SetStateAction } from 'react';
import socketIOCLient from 'socket.io-client';
import Message from '../components/Message/Message';

const BASE_URL = process.env.REACT_APP_BASE_URL;
import {
  Player,
  Room,
  User,
  Boardsetup,
  Hotspot,
  GlobalChoice,
  BoardAction,
  SpecialAction,
  PlayerAction,
} from '../Interfaces/Board.types';
let socket: SocketIOClient.Socket;

interface PlayersState {
  players: Player[];
  setPlayers: Dispatch<SetStateAction<Player[]>>;
}

interface RoomsState {
  rooms: Room[];
  setRooms: Dispatch<SetStateAction<Room[]>>;
}
interface GameState {
  gameStarting: boolean;
  setGameStarting: Dispatch<SetStateAction<boolean>>;
}

interface GameListener {
  setInitPlayers: Dispatch<SetStateAction<Player[]>>;
  setInitGame: Dispatch<SetStateAction<boolean>>;
}

export const adminLogin = (): SocketIOClient.Socket => {
  socket = socketIOCLient(`${BASE_URL}/admin`);
  return socket;
};

export const adminCreateRooms = (roomObjects: Room[]): void => {
  socket.emit('admin-create-rooms', roomObjects);
};

export const initiateSocket = async (): Promise<SocketIOClient.Socket> => {
  socket = socketIOCLient(`${BASE_URL}`);
  return socket;
};

export const joinSocketRoom = (user: User, roomId: number): void => {
  socket.emit('join-room', { user: user, roomId: roomId });
};

export const leaveSocketRoom = (user: User, roomId: number): void => {
  socket.emit('leave-room', { user: user, roomId: roomId });
};

export const disconnectSocket = (): void => {
  if (socket) socket.disconnect();
};

export const sendChatMessage = (user: User, message: string, channel: number | undefined): void => {
  socket.emit('send-user-message', { user: user, message: message, channel: channel });
};

export const updateRoomListener = ({ setRooms }: RoomsState): SocketIOClient.Emitter => {
  return socket.on('send-rooms', (roomsFromServer: Room[]) => {
    setRooms(roomsFromServer);
  });
};

export const sendGameStatus = (status: boolean): void => {
  socket.emit('send-game-status', status);
};

interface MessageProps {
  user: Player;
  message: string;
  channel: number | undefined;
}

// TODO: Append Message-Component?
export const chatListener = (): SocketIOClient.Emitter => {
  return socket.on('send-user-message', (data: MessageProps) => {
    console.log(`getting a message ${data.message} from user: `, data);
    if (data.channel === 99) {
      const messageContainer = document.querySelectorAll('.chat-message-container-content');
      messageContainer.forEach((container) => {
        const messageElement = document.createElement('div');
        messageElement.innerText = `Admin: ${data.message}`;
        container.append(messageElement);
      });
    } else {
      const messageContainer = document.querySelector(`#message-container-${data.channel}`);
      const adminMessageContainer = document.querySelector('#message-container-99');
      const messageElement = document.createElement('div');
      messageElement.classList.add('chat-msg-element');
      messageElement.innerHTML = `<div class="msg-meta">${data.user.name} to room ${data.user.designatedRoom}</div><div class="msg-message">${data.message}</div>`;

      console.log('messagecontainer is: ', messageContainer);
      // messageElement.className = 'user-message';
      // messageElement.innerText = `Room ${data.user.designatedRoom} ${data.user.name}: ${data.message}`;
      console.log('messageelement is: ', messageElement);
      if (messageContainer !== null) {
        messageContainer.append(messageElement);
      }
      if (adminMessageContainer !== null) {
        adminMessageContainer.append(messageElement);
      }
    }
  });
};

export const updateGamestatusListener = ({ setGameStarting }: GameState): SocketIOClient.Emitter => {
  return socket.on('update-game-status', (status: boolean) => {
    setGameStarting(status);
  });
};

export const updatePlayers = (player: Player): void => {
  socket.emit('update-players', player);
};

export const updatePlayersListener = ({ setPlayers }: PlayersState): SocketIOClient.Emitter => {
  return socket.on('update-state-players', (playersFromServer: Player[]) => {
    setPlayers(() => playersFromServer);
  });
};

export const adminChatListener = (): SocketIOClient.Emitter => {
  return socket.on('user-chat-message-to-admin', (data: { user: Player; message: string }) => {
    const messageContainer = document.getElementById(`message-container${data.user.designatedRoom}`);
    const messageElement = document.createElement('div');
    messageElement.innerText = `${data.user.name} wrote: ${data.message}`;
    messageContainer?.append(messageElement);
  });
};

export const startGame = (): void => {
  console.log('socketAPi Startgame');
  socket.emit('start-game');
};

export const startGameListener = ({ setInitGame, setInitPlayers }: GameListener): SocketIOClient.Emitter => {
  return socket.on('start-the-game', (roomPlayers: Player[]) => {
    setInitGame(true);
    setInitPlayers([...roomPlayers]);
  });
};

// Update
export const updatePlayersInRoom = (players: Player[], room: number): void => {
  const stringified = JSON.stringify(players);
  socket.emit('update-players-in-room', { players: stringified, room });
};

export const updatePlayersInRoomListener = (dispatchPlayers: Dispatch<PlayerAction>): SocketIOClient.Emitter => {
  return socket.on('update-players-in-room', (players: string) => {
    const parsedPlayers: Player[] = JSON.parse(players);
    dispatchPlayers({ type: 'UPDATE_PLAYERS', playersArray: [...parsedPlayers] });
  });
};

export const updateStateInRoom = (boardState: Boardsetup, roomID: number): void => {
  const stringified = JSON.stringify(boardState);
  socket.emit('update-state-in-room', { boardState: stringified, roomID });
};

// type Reducer<Boardsetup, BoardAction> = (boardState: Boardsetup, action: BoardAction) => Boardsetup;

export const updateStateInRoomListener = (dispatch: Dispatch<BoardAction>): SocketIOClient.Emitter => {
  return socket.on('update-state-in-room', (receivedState: string) => {
    const upd = JSON.parse(receivedState);
    dispatch({ type: 'GOT_UPDATE', board: upd });
  });
};

// Admin event listeners
export const adminUpdatePlayersListener = (
  setPlayerLog: Dispatch<SetStateAction<Player[]>>
): SocketIOClient.Emitter => {
  return socket.on('update-players-in-room', (players: string) => {
    const parsed: Player[] = JSON.parse(players);
    setPlayerLog((prev) => [...prev, ...parsed]);
  });
};

interface AdminUpdateState {
  state: string;
  room: number;
}

interface StateLog {
  [key: string]: Boardsetup;
}

export const adminUpdateStateListener = (setStateLog: Dispatch<SetStateAction<StateLog>>): SocketIOClient.Emitter => {
  return socket.on('update-state-in-room', (receivedObj: AdminUpdateState) => {
    setStateLog(
      (prev): StateLog => {
        const newState = { ...prev };
        const updState: Boardsetup = JSON.parse(receivedObj.state);
        const roomID: string = JSON.stringify(receivedObj.room);
        newState[roomID] = updState;
        return newState;
      }
    );
  });
};

// Disruptions

export const globalDisruptionTrigger = (roomID: number, left: boolean): void => {
  socket.emit('global-disruption-trigger', { roomID, left });
};

interface RoomInfo {
  from: number;
  to: number;
}

export const globalDisruptionTriggerListener = (boardstate: Boardsetup): SocketIOClient.Emitter => {
  return socket.on('global-disruption-trigger', (roomsInfo: RoomInfo) => {
    socket.emit('global-disruption-response', { from: roomsInfo.from, to: roomsInfo.to, boardstate });
  });
};

interface DisruptionResponse {
  boardstate: Boardsetup;
  to: number;
}

export const neighbourResponseListener = (
  setGlobalChoice: Dispatch<SetStateAction<GlobalChoice>>
): SocketIOClient.Emitter => {
  return socket.on('global-disruption-response', (response: DisruptionResponse) => {
    setGlobalChoice({ ...response, dialog: true });
  });
};

export const globalDisruptionChoice = (position: number, to: number): void => {
  socket.emit('global-disruption-choice', { position, to });
};

export const globalDisruptionChoiceListener = (
  dispatch: Dispatch<BoardAction>,
  hotspots: Hotspot[],
  user: User,
  activeSeat: number,
  setAnnouncement: Dispatch<SetStateAction<string>>
): SocketIOClient.Emitter => {
  return socket.on('global-disruption-choice', (position: number) => {
    const newHotspots = hotspots
      .sort((a, b) => a.position - b.position)
      .map((hotspot) => {
        if (hotspot.position === position) {
          if (hotspot.climmie === 0) {
            hotspot.climmie = 1;
            dispatch({ type: 'CLIMMIE', amount: -1 });
          }
        }
        return hotspot;
      });
    dispatch({ type: 'HOTSPOT', hotspot: [...newHotspots] });
    setAnnouncement(`Your neighbouring table sent you a climmie and placed it on your board.`);
  });
};

export const emissionEmitter = (): void => {
  socket.emit('emission-raise');
};

export const raiseGlobalEmissionListener = (
  setGlobalEmission: Dispatch<SetStateAction<boolean>>
): SocketIOClient.Emitter => {
  return socket.on('raise-global-emissions', () => {
    setGlobalEmission(true);
    // console.log('global emissions raised');
  });
};

// ASK AND MOVE PLAYER ON AGREEMENT
export const askPermissionToMove = (
  askerSeat: number,
  answerSeat: number,
  position: number,
  room: number | undefined
): void => {
  socket.emit('ask-for-premission-to-move', { askerSeat, answerSeat, position, room });
};

interface AskForPermission {
  position: number;
  askerSeat: number;
  answerSeat: number;
}

export const askPermissionToMoveListener = (
  dispatchSpecials: Dispatch<SpecialAction>,
  user: User
): SocketIOClient.Emitter => {
  return socket.on('ask-for-premission-to-move', ({ askerSeat, answerSeat, position }: AskForPermission) => {
    user.seat === answerSeat && dispatchSpecials({ type: 'QUICK', id: position });
  });
};

export const sendPermissionToMove = (
  answerSeat: number,
  answer: boolean,
  position: number,
  room: number | undefined
): void => {
  socket.emit('send-permission-to-move', { answerSeat, answer, position, room });
};

interface PermissionAnswer {
  answerSeat: number;
  answer: boolean;
  position: number;
}

export const sendPermissionToMoveListener = (
  dispatchPlayers: Dispatch<PlayerAction>,
  players: Player[]
): SocketIOClient.Emitter => {
  return socket.on('send-permission-to-move', ({ answerSeat, answer, position }: PermissionAnswer) => {
    const newArray = players.map((player) => {
      if (player.seat === answerSeat) {
        player.position = position;
      }
      return player;
    });
    answer && dispatchPlayers({ type: 'UPDATE_PLAYERS', playersArray: newArray });
  });
};

// The Grand Alliance
// Trigger
export const theGrandAllianceTrigger = (room: number, amount: number): void => {
  socket.emit('grand-alliance-trigger', { room, amount });
};
// Listener
interface TgaListener {
  room: number;
  amount: number;
}

export const theGrandAllianceTriggerListener = (dispatchSpecials: Dispatch<SpecialAction>): SocketIOClient.Emitter => {
  return socket.on('grand-alliance-trigger', ({ room, amount }: TgaListener) => {
    dispatchSpecials({ type: 'GRAND', id: room });
    dispatchSpecials({ type: 'GRAND_AMOUNT', id: amount });
  });
};

// ---> user input with slider for the current player

// Pay picker on submit trigger
export const theGrandAllianceOffer = (amount: number, room: number): void => {
  socket.emit('grand-alliance-offer', { amount, room });
};
// Pay picker on submit listener
export const theGrandAllianceOfferListener = (
  dispatchSpecials: Dispatch<SpecialAction>,
  grandRaised: number,
  grandAnswers: number
): SocketIOClient.Emitter => {
  return socket.on('grand-alliance-offer', (amount: number) => {
    const raised = amount + grandRaised;
    const answers = grandAnswers + 1;
    dispatchSpecials({ type: 'GRAND_RAISED', id: raised });
    dispatchSpecials({ type: 'GRAND_ANSWERS', id: answers });
  });
};

// ---> waiting for all teams to submit

// Succesful or not emitter
export const theGrandAllianceFinal = (success: boolean): void => {
  socket.emit('grand-alliance-final', { success });
};
// Succesful or not listener
export const theGrandAllianceFinalListener = (dispatchSpecials: Dispatch<SpecialAction>): SocketIOClient.Emitter => {
  return socket.on('grand-alliance-final', (success: boolean): void => {
    success ? dispatchSpecials({ type: 'GRAND_SUCCESS', id: 2 }) : dispatchSpecials({ type: 'GRAND_SUCCESS', id: 1 });
    dispatchSpecials({ type: 'GRAND', id: -1 });
  });
};

