import './SelectedCard.scss';
import InitiativeCard from '../InitiativeCards';
import { Dispatch, FunctionComponent } from 'react';
import React from 'react';
import { IDisruptionCard, IInitiativeCard, SpecialAction, Player, User } from '../../Interfaces/Board.types';
import { isDisruptionCard } from '../Utils/Helpers';
import DisruptionCard from '../DisruptionCards';

interface Props {
  card: IInitiativeCard | IDisruptionCard | undefined;
  setSelectedCard(id: number): void;
  dispatchSpecials: Dispatch<SpecialAction>;
  selectedCard: number;
  isItPossibleToPlayThisCard(id: number): boolean;
  again: boolean;
  removeSelectedCard(): void;
  playSelectedCard(): void;
  getCurrentPlayer(players: Player[], activeSeat: number, user: User): Player | undefined;
  discardedCard: boolean;
  discardOneInitiativeCard(id: number): void;
  players: Player[];
  activeSeat: number;
  user: User;
}

const SelectedCard: FunctionComponent<Props> = ({
  card,
  setSelectedCard,
  dispatchSpecials,
  selectedCard,
  isItPossibleToPlayThisCard,
  again,
  removeSelectedCard,
  playSelectedCard,
  getCurrentPlayer,
  discardedCard,
  discardOneInitiativeCard,
  players,
  activeSeat,
  user,
}: Props) => {
  const buttonDisplay: JSX.Element | null = card ? (
    isDisruptionCard(card) ? (
      <button onClick={removeSelectedCard} className="play-selected-card-btn display-off button">
        Oh well...
      </button>
    ) : isItPossibleToPlayThisCard(selectedCard) ? (
      <button onClick={playSelectedCard} className="play-selected-card-btn display-off button">
        Play Selected Card
      </button>
    ) : null
  ) : null;

  const discardHandler = () => {
    discardOneInitiativeCard(selectedCard);
  };

  return (
    <div className="selected-card-display">
      <div className="selected-card-greyed-out display-off" onClick={removeSelectedCard}></div>
      {card ? (
        isDisruptionCard(card) ? (
          <div className="selected-card">
            <DisruptionCard card={card} user={user} />
          </div>
        ) : (
          <div className="selected-card">
            <InitiativeCard
              user={user}
              key={card.id}
              cardId={card.id}
              dispatchSpecials={dispatchSpecials}
              again={again}
              setSelectedCard={setSelectedCard}
            />
          </div>
        )
      ) : null}
      {buttonDisplay}
      {getCurrentPlayer(players, activeSeat, user) &&
      getCurrentPlayer(players, activeSeat, user)?.remainingActions === 0 &&
      !discardedCard &&
      card &&
      !isDisruptionCard(card) &&
      card.cost !== 0 ? (
        <button className="discard-selected-card-btn button" onClick={discardHandler}>
          Discard Selected Card
        </button>
      ) : null}
    </div>
  );
};
export default SelectedCard;
