import React, { FunctionComponent, useEffect, useState, Dispatch, SetStateAction } from 'react';
import { BoardAction, Role, Player, User } from '../../Interfaces/Board.types';
import './Tutorial.scss';
import '../Board/Board.scss';
import Welcome from './Welcome';
import GlobalEmissions from './GlobalEmissions';
import Pathways from './Pathways';
import Shields from './Shields';
import Roles from './Roles';
import Initiative from './Initiative';
import Funding from './Funding';
import Turns from './Turns';
import Disruption from './Disruption';
import Bailout from './Bailout';
import EndOfTurn from './EndOfTurn';
import Connected from './Connected';
import Zillians from './Zillians';
import Race from './Race';
import Reminder from './Reminder';
import { tutorialReady, tutorialReadyListener } from '../../services/SocketApi';

interface P {
  dispatch: Dispatch<BoardAction>;
  playEmission: () => void;
  tutorialHighlighter: (className: string) => void;
  tutorialReseter: (className: string) => void;
  tutorialGreyOut: (className: string) => void;
  tutorialRemoveGreyOut: (className: string) => void;
  tutorialHandRemove: () => void;
  tutorialHand: () => void;
  socket: SocketIOClient.Socket;
  myRole: Role;
  user: User;
  players: Player[];
  activeSeat: number;
  updatePlayersInRoom: (players: Player[], room: number) => void;
  setTutorialRole: Dispatch<SetStateAction<boolean>>;
  isMute: boolean;
}
const Tutorial: FunctionComponent<P> = ({
  dispatch,
  playEmission,
  tutorialHighlighter,
  tutorialReseter,
  tutorialGreyOut,
  tutorialRemoveGreyOut,
  tutorialHandRemove,
  tutorialHand,
  socket,
  players,
  user,
  isMute,
  activeSeat,
  updatePlayersInRoom,
  myRole,
  setTutorialRole,
}: P): JSX.Element => {
  const [step, setStep] = useState(1);
  const [numReady, setNumReady] = useState<number>(0);
  const [ready, setReady] = useState<boolean>(false);

  useEffect(() => {
    tutorialReadyListener(setNumReady);
  }, [socket]);

  useEffect(() => {
    if (step === 16) {
      tutorialRemoveGreyOut('.tut-board');
      tutorialHighlighter('.carbion');
      tutorialRemoveGreyOut('.player-display-frame');
      dispatch({ type: 'TUTORIAL' });
    }
  }, [step]);

  useEffect(() => {
    if (numReady === players.length) {
      resetAll();
      if (step === 4) setTutorialRole(true);
      setStep((prev) => prev + 1);
      setReady(false);
      setNumReady(0);
    }
  }, [numReady]);

  const resetAll = (): void => {
    tutorialReseter('.core');
    tutorialReseter('.start');
    tutorialReseter('.spot');
    tutorialReseter('.shield');
    tutorialReseter('.money');
    tutorialReseter('.victory-track');
    tutorialReseter('.player');
    tutorialReseter('.zillian');
    tutorialReseter('.disruption-clouds');
    tutorialReseter('.carbion');
    tutorialReseter('.climmie');
    tutorialReseter('.earning-level');
    tutorialHandRemove();
  };

  const nextStep = (): void => {
    if (!ready) {
      setReady(true);
      setNumReady((prev) => prev + 1);
      user.designatedRoom !== undefined && tutorialReady(user.designatedRoom);
    }
    if (step === 1 && user.seat === activeSeat && user.designatedRoom !== undefined) {
      const room = user.designatedRoom;
      const newPlayers = players.slice();
      updatePlayersInRoom(newPlayers, room);
    }
  };

  return (
    <div className="tutorial-container">
      {step === 1 ? <Welcome tutorialGreyOut={tutorialGreyOut} tutorialHighlighter={tutorialHighlighter} /> : null}
      {step === 2 ? (
        <GlobalEmissions isMute={isMute} tutorialHighlighter={tutorialHighlighter} playEmission={playEmission} />
      ) : null}
      {step === 3 ? <Pathways tutorialHighlighter={tutorialHighlighter} /> : null}
      {step === 4 ? <Shields tutorialHighlighter={tutorialHighlighter} /> : null}
      {step === 5 && myRole ? <Roles myRole={myRole} user={user} tutorialHighlighter={tutorialHighlighter} /> : null}
      {step === 6 ? <Initiative tutorialHighlighter={tutorialHighlighter} tutorialHand={tutorialHand} /> : null}
      {step === 7 ? <Funding tutorialHighlighter={tutorialHighlighter} /> : null}
      {step === 8 ? <Turns tutorialHighlighter={tutorialHighlighter} /> : null}
      {step === 9 ? <Disruption tutorialHighlighter={tutorialHighlighter} /> : null}
      {step === 10 ? <Bailout tutorialHighlighter={tutorialHighlighter} /> : null}
      {step === 11 ? <EndOfTurn tutorialHighlighter={tutorialHighlighter} /> : null}
      {step === 12 ? <Connected tutorialHighlighter={tutorialHighlighter} /> : null}
      {step === 13 ? <Zillians tutorialHighlighter={tutorialHighlighter} /> : null}
      {step === 14 ? <Race tutorialHighlighter={tutorialHighlighter} /> : null}
      {step === 15 ? <Reminder tutorialHighlighter={tutorialHighlighter} /> : null}
      {ready ? (
        step !== 9 && step !== 10 ? (
          <button className="waiting-for-players button">Waiting for other players...</button>
        ) : (
          <button className="waiting-for-players waiting-climmie-spot button">Waiting for other players...</button>
        )
      ) : step !== 9 && step !== 10 ? (
        <button onClick={nextStep} className="tut-button button">
          Next
        </button>
      ) : (
        <button onClick={nextStep} className="tut-button-disruption button tut-button">
          Next
        </button>
      )}
    </div>
  );
};

export default Tutorial;
