import React, { FunctionComponent, Dispatch } from 'react';
import { Player, User, SpecialAction } from '../../Interfaces/Board.types';
import InitiativeCard from '../InitiativeCards';
import './PlayersHand.scss';

interface Props {
  user: User;
  players: Player[];
  activeSeat: number;
  again: boolean;
  dispatchSpecials: Dispatch<SpecialAction>;
  setSelectedCard(id: number): void;
}

const PlayersHand: FunctionComponent<Props> = ({ user, players, setSelectedCard, dispatchSpecials, again }: Props) => {
  const player = players.find((player) => player.seat === user.seat);

  return (
    <div className="players-hand">
      {player?.hand.map((cardId, i) => {
        return (
          <div key={i} className="card">
            <InitiativeCard
              key={i}
              cardId={cardId}
              user={user}
              dispatchSpecials={dispatchSpecials}
              again={again}
              setSelectedCard={setSelectedCard}
            />
          </div>
        );
      })}
    </div>
  );
};

export default PlayersHand;
