import React, { Dispatch, FunctionComponent, SetStateAction, useEffect, useState } from 'react';
import RoomComponent from '../RoomComponent/RoomComponent';
import ChatBox from '../ChatBox/ChatBox';
import { Player, Room, User } from '../../Interfaces/Board.types';
import './Lobby.scss';
import Chatmessages from '../Chatmessages/Chatmessages';
import { useHistory } from 'react-router';

export interface Props {
  joinRoom: (roomId: number) => void;
  leaveRoom: (roomId: number) => void;
  user: User;
  players: Player[];
  rooms: Room[];
  gameStarting: boolean;
  initGame: boolean;
  setUser: Dispatch<SetStateAction<User>>;
}

const Lobby: FunctionComponent<Props> = ({
  setUser,
  rooms,
  user,
  joinRoom,
  leaveRoom,
  gameStarting,
  initGame,
}: Props) => {
  const history = useHistory();
  const [language, setLanguage] = useState<string>('en');

  useEffect(() => {
    if (initGame) {
      console.log('init game from lobby fired');
      if (user.designatedRoom !== undefined) {
        user.boardId = setUser({ ...user, boardId: rooms[user.designatedRoom]._id, language: language });
      }
      setTimeout(() => {
        history.push(`/board/${user.designatedRoom}`);
      }, 2000);
    }
  }, [initGame]);

  return (
    <div className="lobby-container">
      <div className="lobby-content-container">
        <div className="lobby-header">Game Lobby</div>
        <div id="lobby-language-labels" className="lobby-language-container">
          <label htmlFor="language">Language</label>
          <select defaultValue="English" onChange={(e) => setLanguage(e.target.value)}>
            <option value="en">English</option>
            <option value="nl">Dutch</option>
          </select>
        </div>
        <div className="lobby-sub-header">Scroll down to see all available tables</div>
        {rooms.map((room) => (
          <div key={`lobby-${room.id}`} className="lobby-content-repeat">
            <div className="lobby-rooms-container">
              <RoomComponent
                room={room}
                joinRoom={joinRoom}
                user={user}
                leaveRoom={leaveRoom}
                gameStarting={gameStarting}
              />
            </div>
            <div className="lobby-messages-container" id={`messages-${room.id}`}>
              <Chatmessages channel={room.id} />
              <ChatBox user={user} channel={room.id} />
            </div>
            {/* <div className="lobby-input-container">
            </div> */}
          </div>
        ))}
      </div>
    </div>
  );
};

export default Lobby;
