import { gsap } from 'gsap';
import { TweenMax } from 'gsap';

const cloudAnimation = (): void => {
  const cloud = document.querySelector('.the-cloud');
  gsap.fromTo(
    cloud,
    {
      y: -16,
      x: 4,
      yoyo: true,
      opacity: 0.7,
    },
    {
      y: 0,
      x: 0,
      yoyo: true,
      opacity: 0.9,
      repeat: -1,
      duration: 8,
      ease: 'easeInOut',
    }
  );
};

