import { Specials, SpecialAction } from '../Interfaces/Board.types';

const initSpecials: Specials = {
  alex: false,
  chrisEffectFired: false,
  initiativesTaken: 0,
  gaby: false,
  max: false,
  sam: false,
  grand: -1,
  grandAmount: -1,
  grandRaised: 0,
  grandAnswers: 0,
  grandSuccess: 0,
  grandOffer: 0,
  quick: -1,
  again: false,
  againId: 0,
  sharing: false,
  crystal: false,
  crystals: [],
  bets: false,
  onChoice: false,
};

const specialReducer = (specials: Specials, specialAction: SpecialAction): Specials => {
  switch (specialAction.type) {
    case 'ALEX':
      return { ...specials, alex: !specials.alex };
    case 'CHRIS':
      return specialAction.setTo !== undefined ? { ...specials, chrisEffectFired: specialAction.setTo } : specials;
    case 'INITIATIVES_TAKEN':
      return { ...specials, initiativesTaken: specials.initiativesTaken + 1 };
    case 'RESET_INITIATIVES_TAKEN':
      return { ...specials, initiativesTaken: 0 };
    case 'GABY':
      return { ...specials, gaby: !specials.gaby };
    case 'MAX':
      return specialAction.setTo !== undefined ? { ...specials, max: specialAction.setTo } : specials;
    case 'SAM':
      return { ...specials, sam: !specials.sam };
    case 'GRAND':
      return specialAction.id !== undefined ? { ...specials, grand: specialAction.id } : specials;
    case 'GRAND_AMOUNT':
      return specialAction.id !== undefined ? { ...specials, grandAmount: specialAction.id } : specials;
    case 'GRAND_RAISED':
      return specialAction.id !== undefined
        ? { ...specials, grandRaised: specials.grandRaised + specialAction.id }
        : specials;
    case 'GRAND_ANSWERS':
      return specialAction.id !== undefined
        ? { ...specials, grandAnswers: specials.grandAnswers + specialAction.id }
        : specials;
    case 'GRAND_SUCCESS':
      return specialAction.id !== undefined ? { ...specials, grandSuccess: specialAction.id } : specials;
    case 'GRAND_OFFER':
      return specialAction.id !== undefined ? { ...specials, grandOffer: specialAction.id } : specials;
    case 'QUICK':
      return specialAction.id !== undefined ? { ...specials, quick: specialAction.id } : specials;
    case 'AGAIN':
      return specialAction.setTo !== undefined ? { ...specials, again: specialAction.setTo } : specials;
    case 'AGAIN_ID':
      return specialAction.id !== undefined ? { ...specials, againId: specialAction.id } : specials;
    case 'SHARING':
      return { ...specials, sharing: !specials.sharing };
    case 'CRYSTAL':
      return specialAction.setTo !== undefined ? { ...specials, crystal: specialAction.setTo } : specials;
    case 'CRYSTALS':
      return specialAction.array ? { ...specials, crystals: specialAction.array } : specials;
    case 'BETS':
      return specialAction.setTo !== undefined ? { ...specials, bets: specialAction.setTo } : specials;
    case 'ONCHOICE':
      return specialAction.setTo !== undefined ? { ...specials, onChoice: specialAction.setTo } : specials;
    default:
      throw new Error('Something went wrong in special reducer');
  }
};

export { initSpecials, specialReducer };
