import { Player, User } from '../../Interfaces/Board.types';

const getCurrentPlayer = (players: Player[], activeSeat: number, user: User): Player | undefined => {
  const currentPlayer = players
    .map((pl) => {
      if (pl.seat === activeSeat && pl.seat === user.seat) return pl;
    })
    .filter((el) => el !== undefined)[0];
  if (currentPlayer) return currentPlayer;
};

const getSeatPlayer = (seat: number, players: Player[]): Player | undefined => {
  const seatPlayer = players
    .map((pl) => {
      if (pl.seat === seat) return pl;
    })
    .filter((el) => el !== undefined)[0];
  if (seatPlayer) return seatPlayer;
};

const validActionCheck = (players: Player[], activeSeat: number, user: User): boolean => {
  const currentPlayer = getCurrentPlayer(players, activeSeat, user);
  if (currentPlayer?.seat === activeSeat && currentPlayer.remainingActions > 0) return true;
  return false;
};

const getCurrentPosition = (players: Player[], seat: number): number => {
  const playerPosition = players.find((player) => player.seat === seat)?.position;
  return playerPosition ? playerPosition : -1;
};

const totalCardsMissing = (players: Player[]): number => {
  let total = 0;
  players.forEach((player) => {
    total += 3 - player.hand.length;
  });
  return total;
};

const refillHand = (player: Player, InitiativeDeck: number[]): Player => {
  const numOfCardsToDraw = 3 - player.hand.length;
  for (let i = 0; i < numOfCardsToDraw; i++) {
    const drawnCard = InitiativeDeck.pop();
    drawnCard && player.hand.push(drawnCard);
  }
  return player;
};

export { getCurrentPlayer, getSeatPlayer, validActionCheck, getCurrentPosition, totalCardsMissing, refillHand };
