import { IDisruptionCard, IInitiativeCard, Role } from '../../Interfaces/Board.types';
import { initiativeDeckData } from '../InitiativeCards/InitiativeCards';
import { disruptionDeckData } from '../DisruptionCards/DisruptionCards';
import { roleDeckData } from '../RoleCards/RoleCards';

function shuffle(list: number[]): number[] {
  for (let i = list.length - 1; i > 0; i--) {
    const j = Math.floor(Math.random() * (i + 1));
    [list[i], list[j]] = [list[j], list[i]];
  }
  return list;
}

function shuffleNames(list: string[]): string[] {
  for (let i = list.length - 1; i > 0; i--) {
    const j = Math.floor(Math.random() * (i + 1));
    [list[i], list[j]] = [list[j], list[i]];
  }
  return list;
}

const getCardFromId = (id: number): IInitiativeCard | IDisruptionCard | undefined => {
  return id < 40
    ? initiativeDeckData.find((card) => card.id === id)
    : disruptionDeckData.find((card) => card.id === id);
};

const getRoleFromId = (id: number): Role | undefined => {
  return roleDeckData.find((role) => role.id === id);
};

const isInitiativeCard = (card: IInitiativeCard | IDisruptionCard): card is IInitiativeCard => {
  return (card as IInitiativeCard).realityCheck !== undefined;
};

const isDisruptionCard = (card: IInitiativeCard | IDisruptionCard): card is IDisruptionCard => {
  return (card as IDisruptionCard).globalEffect !== undefined;
};

const shufflePlayerImages = (): string => {
  const number = Math.ceil(Math.random() * 3);
  if (number === 1) return 'zil.png';
  else if (number === 2) return 'carb.png';
  else if (number === 3) return 'climmie.png';
  else return 'man-3-svg';
};

export { shufflePlayerImages, shuffle, getCardFromId, isDisruptionCard, isInitiativeCard, getRoleFromId, shuffleNames };
