import React, { FunctionComponent, useEffect } from 'react';
import './Tutorial.scss';

interface P {
  tutorialGreyOut: (className: string) => void;
  tutorialHighlighter: (className: string) => void;
}

const Welcome: FunctionComponent<P> = ({ tutorialGreyOut, tutorialHighlighter }: P): JSX.Element => {
  useEffect(() => {
    tutorialGreyOut('.tut-board');
    tutorialGreyOut('.player-display-frame');
    tutorialHighlighter('.zillian');
  }, []);

  return (
    <>
      <div className="dialog-box dialog-box-zillian">
        <div>
          <p className="minion-message">
            Welcome to Minions of Disruption! My name is Zillian. The other minions and I will help you to get started
            with the game.
            <br />
            <br />
            When you understood press next. We will wait for all of you to complete each step before continuing.
          </p>
        </div>
      </div>
      <div className="dialog-box-pointer"></div>
    </>
  );
};

export default Welcome;
