import { Hotspot } from '../../Interfaces/Board.types';

const displayDiscardedCards = (): void => {
  document.querySelector('.discarded-display')?.classList.remove('display-off');
  document.querySelector('.discarded-display-grey-out')?.classList.remove('display-off');
};

const hideDiscardedCards = (): void => {
  document.querySelector('.discarded-display')?.classList.add('display-off');
  document.querySelector('.discarded-display-grey-out')?.classList.add('display-off');
};

const toggleClickableCore = (): void => {
  const coreEl = document.querySelector('.core');
  const coreHighlight = document.querySelector('.core-highlight');
  if (coreEl && coreHighlight) {
    if (coreEl.classList.contains('clickable-hotspot')) {
      coreEl.classList.add('core-hover-on');
      coreHighlight.classList.remove('display-off');
    } else {
      coreEl.classList.remove('core-hover-on');
      coreHighlight.classList.add('display-off');
    }
  }
};

const createHotspotArray = (): Element[] => {
  const hotspotArray = [
    ...Array.from(document.querySelectorAll('.core')),
    ...Array.from(document.querySelectorAll('.start')),
    ...Array.from(document.querySelectorAll('.spot')),
  ];
  return hotspotArray;
};

const resetEventListenersAndClasses = (): void => {
  createHotspotArray().forEach((element) => {
    element.classList.remove('clickable-hotspot');
    toggleClickableCore();
  });
};

const resetShieldListenersAndClasses = (): void => {
  document.querySelector('.choose')?.classList.remove('greyed-out-shield');
  Array.from(document.querySelectorAll('.shield')).forEach((shield) => {
    shield.classList.remove('shield-highlight');
    const newNode = shield.cloneNode(true);
    shield.parentNode?.replaceChild(newNode, shield);
  });
};

const updateClimmieClasses = (hotspots: Hotspot[]): void => {
  hotspots.forEach((hotspot) => {
    if (hotspot.position !== 0) {
      if (hotspot.climmie === 1) {
        document.querySelector(`.board-element.spot${hotspot.position}`)?.classList.add('climmie-on-hotspot');
      } else {
        document.querySelector(`.board-element.spot${hotspot.position}`)?.classList.remove('climmie-on-hotspot');
      }
    }
  });
};

const onMouseEnterCarbion = (): void => {
  document.querySelector('.board-message-container-carbion')?.classList.remove('display-off');
};

const onMouseLeaveCarbion = (): void => {
  document.querySelector('.board-message-container-carbion')?.classList.add('display-off');
};

export {
  displayDiscardedCards,
  hideDiscardedCards,
  toggleClickableCore,
  createHotspotArray,
  resetEventListenersAndClasses,
  resetShieldListenersAndClasses,
  updateClimmieClasses,
  onMouseLeaveCarbion,
  onMouseEnterCarbion,
};
