import React, { FunctionComponent, useEffect } from 'react';
import './Tutorial.scss';

interface P {
  tutorialHighlighter: (className: string) => void;
}

const Pathways: FunctionComponent<P> = ({ tutorialHighlighter }: P): JSX.Element => {
  useEffect(() => {
    tutorialHighlighter('.core');
    tutorialHighlighter('.spot');
    tutorialHighlighter('.start');
    tutorialHighlighter('.zillian');
  }, []);

  return (
    <>
      <div className="dialog-box dialog-box-zillian">
        <div>
          <p className="minion-message">
            In the center of the board is the Core. There are 8 pathways leading to the core: each represents an
            important function of your neighbourhood.
            <br />
            <br />
            There are 2 Hotspots on each pathway. Hotspots are vulnerable parts of your neighbourhood that are open to
            climate disruptions.
          </p>
        </div>
      </div>
      <div className="dialog-box-pointer"></div>
    </>
  );
};

export default Pathways;
