/* eslint-disable @typescript-eslint/no-unused-vars */
import React, { useEffect, useState, useReducer, FunctionComponent, MouseEvent } from 'react';
import './Board.scss';
import { compose } from 'ramda';
import { Player, Hotspot, BoardProps, GlobalChoice, User, Role } from '../../Interfaces/Board.types';
import { specialReducer, initSpecials, boardReducer, initialBoardSetup, playersReducer } from '../../Reducers';
import { shuffle, getCardFromId, isInitiativeCard, getRoleFromId, isDisruptionCard } from '../Utils/Helpers';
import {
  updatePlayersInRoom,
  updatePlayersInRoomListener,
  updateStateInRoom,
  updateStateInRoomListener,
  globalDisruptionTrigger,
  globalDisruptionTriggerListener,
  neighbourResponseListener,
  globalDisruptionChoice,
  globalDisruptionChoiceListener,
  raiseGlobalEmissionListener,
  askPermissionToMove,
  askPermissionToMoveListener,
  sendPermissionToMove,
  sendPermissionToMoveListener,
  theGrandAllianceTrigger,
  theGrandAllianceTriggerListener,
  theGrandAllianceOffer,
  theGrandAllianceOfferListener,
  theGrandAllianceFinal,
  theGrandAllianceFinalListener,
} from '../../services/SocketApi';
import { cloudAnimation, carbionAnimation, moveShield } from './Animations';
import { updateBoard, loadBoard } from '../../services/ServerApi';
import {
  useSound,
  carbion,
  coin,
  climmie,
  core,
  zillian,
  pickcard,
  dropcard,
  bailout,
  escalate,
  endturn,
  grandalliance,
  move,
  clicksound,
  wongame,
  ohhwee,
  pathway,
  removecarbion,
  removeclimmie,
  emission,
  Tutorial,
  PlayersHand,
  CardDecksDisplay,
  PlayerPawn,
  PlayerDisplay,
  DisruptionCloud,
  ShieldSVG,
  SelectedCard,
  DiscardedCardsDisplay,
  TopThreeCards,
  DialogBox,
  Announcement,
  ReferenceDisplay,
  OtherPlayersCardsDisplay,
  setTimeout,
  defaultRole,
  getCurrentPlayer,
  getSeatPlayer,
  validActionCheck,
  getCurrentPosition,
  totalCardsMissing,
  // refillHand,
  displayDiscardedCards,
  hideDiscardedCards,
  toggleClickableCore,
  createHotspotArray,
  resetEventListenersAndClasses,
  resetShieldListenersAndClasses,
  updateClimmieClasses,
  onMouseLeaveCarbion,
  onMouseEnterCarbion,
} from '../../components';

const Board: FunctionComponent<BoardProps> = ({ setRouting, initPlayers, user, socket, rooms }: BoardProps) => {
  window.addEventListener('beforeunload', function (e) {
    e.returnValue = 'Are you sure you want to leave the current game? All progress might be lost!';
  });
  const [boardState, dispatch] = useReducer(boardReducer, initialBoardSetup);
  const {
    score,
    escalation,
    carbions,
    climmies,
    coins,
    pathwayTokens,
    shields,
    initiativeDeck,
    disruptionDeck,
    discardedInitiativeCards,
    discardedDisruptionCards,
    hotspots,
    activeSeat,
    roleDeck,
    rolesAssigned,
    wonGame,
    tutorial,
  } = boardState;
  const [players, dispatchPlayers] = useReducer(playersReducer, initPlayers);
  const [specials, dispatchSpecials] = useReducer(specialReducer, initSpecials);
  const {
    alex,
    chrisEffectFired,
    initiativesTaken,
    gaby,
    max,
    sam,
    grand,
    grandAmount,
    grandRaised,
    grandAnswers,
    grandSuccess,
    grandOffer,
    quick,
    again,
    againId,
    sharing,
    crystal,
    crystals,
    bets,
    onChoice,
  } = specials;
  const [referenceDisplay, setReferenceDisplay] = useState(false);
  const [possibleHotspots, setPossibleHotspots] = useState<number[]>([]);
  const [toBeDrawn, setToBeDrawn] = useState<boolean>(false);
  const [selectedCard, setSelectedCard] = useState<number>();
  const [otherPlayer, setOtherPlayer] = useState<Player | undefined>();
  const [globalChoice, setGlobalChoice] = useState<GlobalChoice>({ to: null });
  const [announcement, setAnnouncement] = useState<string>('');
  const [selectPlayer, setSelectPlayer] = useState<boolean>(false);
  const [chosenPlayer, setChosenPlayer] = useState<number>(-1);
  const [disruptionDrawn, setDisruptionDrawn] = useState<boolean>(false);
  const [fundingCollected, setFundingCollected] = useState<boolean>(false);
  const [discardedCard, setDiscardedCard] = useState<boolean>(false);
  const [globalEmission, setGlobalEmission] = useState<boolean>(false);
  const [myRole, setMyRole] = useState<Role>(defaultRole);
  const [tutorialRole, setTutorialRole] = useState<boolean>(false);

  const [isMute, setIsMute] = useState(false);
  const [playClimmie] = useSound(climmie);
  const [playCore] = useSound(core);
  const [playCarbion] = useSound(carbion);
  const [playPickcard] = useSound(pickcard);
  const [playDropcard] = useSound(dropcard);
  const [playZillian] = useSound(zillian, { volume: 0.6 });
  const [playCoin] = useSound(coin, { interrupt: true, volume: 0.3 });
  const [playBailout] = useSound(bailout, { volume: 0.2 });
  const [playEscalate] = useSound(escalate, { volume: 0.2 });
  const [playEndturn] = useSound(endturn, { volume: 0.2 });
  const [playGrandalliance] = useSound(grandalliance, { volume: 0.2 });
  const [playMove] = useSound(move, { volume: 0.2 });
  const [playClicksound] = useSound(clicksound, { volume: 0.2 });
  const [playWongame] = useSound(wongame, { volume: 0.2 });
  const [playOhhwee] = useSound(ohhwee, { volume: 0.2 });
  const [playPathway] = useSound(pathway, { volume: 0.5 });
  const [playRemovecarbion] = useSound(removecarbion, { volume: 0.2 });
  const [playRemoveclimmie] = useSound(removeclimmie, { volume: 0.2 });
  const [playEmission] = useSound(emission, { volume: 0.2 });

  useEffect(() => {
    if (socket) {
      updateStateInRoomListener(dispatch);
      updatePlayersInRoomListener(dispatchPlayers);
      globalDisruptionTriggerListener(boardState);
      neighbourResponseListener(setGlobalChoice);
      globalDisruptionChoiceListener(dispatch, hotspots, user, activeSeat, setAnnouncement);
      raiseGlobalEmissionListener(setGlobalEmission);
      askPermissionToMoveListener(dispatchSpecials, user);
      sendPermissionToMoveListener(dispatchPlayers, players);
      theGrandAllianceTriggerListener(dispatchSpecials);
      theGrandAllianceOfferListener(dispatchSpecials, grandRaised, grandAnswers);
      theGrandAllianceFinalListener(dispatchSpecials);
    }
  }, [socket]);

  useEffect(() => {
    if (user.seat === activeSeat && user.designatedRoom !== undefined) {
      const room = user.designatedRoom;
      updatePlayersInRoom(players, room);
    }
  }, [players]);

  useEffect(() => {
    if (user.seat === activeSeat) {
      const room = user.designatedRoom || 0;
      updateStateInRoom(boardState, room);
    }
  }, [boardState]);

  useEffect(() => {
    updateClimmieClasses(hotspots);
  }, [hotspots]);

  useEffect(() => {
    if (tutorialRole && rolesAssigned) {
      const currentRole = players.find((player) => player.seat === user.seat)?.role;
      currentRole && setMyRole(currentRole);
    }
  }, [tutorialRole]);

  useEffect(() => {
    const hotspotArray = createHotspotArray();
    if (globalChoice.to !== null) {
      globalChoice.boardstate?.hotspots.map((hotspot, i) => {
        if (!hotspot.climmie && hotspot.position !== 0) {
          hotspotArray[i].classList.add('send-climmie-hotspot');
        }
      });
    } else {
      hotspotArray.forEach((hotspot) => hotspot.classList.remove('send-climmie-hotspot'));
    }
  }, [globalChoice]);

  useEffect(() => {
    const moveHotspots = createHotspotArray();
    if (chosenPlayer >= 0) {
      moveHotspots.forEach((hotspot) => {
        hotspot.classList.remove('clickable-hotspot');
        toggleClickableCore();
      });
      moveHotspots.forEach((hotspot) => hotspot.classList.add('move-teammate-to-hotspot'));
    } else {
      moveHotspots.forEach((hotspot) => hotspot.classList.remove('move-teammate-to-hotspot'));
      moveHotspots.forEach((hotspot, i) => {
        if (possibleHotspots.includes(i)) {
          hotspot.classList.add('clickable-hotspot');
          toggleClickableCore();
        }
      });
    }
  }, [chosenPlayer]);

  useEffect(() => {
    if (announcement !== '') {
      setTimeout(() => setAnnouncement(''), 5000);
    }
  }, [announcement]);

  useEffect(() => {
    if (discardedCard) {
      document.querySelector('.card-deck-display_initiative-deck')?.classList.add('card-deck-click');
    } else {
      document.querySelector('.card-deck-display_initiative-deck')?.classList.remove('card-deck-click');
    }
  }, [discardedCard]);

  useEffect(() => {
    selectedCard && document.querySelector(`.selected-card-greyed-out`)?.classList.remove('display-off');
    selectedCard && document.querySelector(`.play-selected-card-btn`)?.classList.remove('display-off');
    const cardName = selectedCard ? getCardFromId(selectedCard)?.id : null;
    document.querySelector(`.card${cardName}`)?.classList.add('selected-card');
    // TODO add animation to special card label (on players hand card instead of selected card display at the moment)
    // if (selectedCard && getCardFromId(selectedCard)) {
    //   if (getCardFromId(selectedCard)?.effects === 'special') {
    //     const selectedSpecialCard = document.querySelector('.special-card-label');
    //     if (selectedSpecialCard) {
    //       gsap.to(selectedSpecialCard, { rotate: 360, duration: 1, transformOrigin: '50% 50%' }).delay(0.5);
    //     }
    //   }
    // }
  }, [selectedCard]);

  useEffect(() => {
    const OtherPlayersCardsElement = document.querySelector('.selected-cards');
    const greyout = document.querySelector('.selected-cards-grey-out');
    if (OtherPlayersCardsElement && greyout) {
      if (otherPlayer !== undefined) {
        OtherPlayersCardsElement.classList.remove('display-off');
        greyout.classList.remove('display-off');
      } else {
        OtherPlayersCardsElement.classList.add('display-off');
        greyout.classList.add('display-off');
      }
    }
  }, [otherPlayer]);

  useEffect(() => {
    setRouting && setRouting(false);
    const currentPlayer = getCurrentPlayer(players, activeSeat, user);
    const shuffledInitiativeDeck = shuffle(initiativeDeck);
    const shuffledDisruptionDeck = shuffle(disruptionDeck);
    const shuffledRoleDeck = shuffle(roleDeck);
    dispatch({ type: 'IDECK', deck: shuffledInitiativeDeck });
    dispatch({ type: 'DDECK', deck: shuffledDisruptionDeck });
    dispatch({ type: 'ROLES', deck: shuffledRoleDeck });
    refillAllHands();
    dispatchPlayers({ type: 'REFRESH_ACTIONS', current: currentPlayer, actions: 4 });
    document.querySelector('.card-deck-display_disruption-deck')?.classList.remove('card-deck-click');
    // Animations
    cloudAnimation();
    carbionAnimation();
  }, []);

  useEffect(() => {
    const currentPlayer = getCurrentPlayer(players, activeSeat, user);
    if (currentPlayer && !rolesAssigned) assignRoles();
    dispatch({ type: 'ROLES_ASSIGNED' });
  }, [roleDeck]);

  useEffect(() => {
    const currentPlayer = getCurrentPlayer(players, activeSeat, user);
    if (currentPlayer) {
      const currentPosition = currentPlayer.position;
      let possibleTargets: number[] = [];
      hotspots.forEach((hotspot) => {
        if (hotspot.position === currentPosition) possibleTargets = [...possibleTargets, ...hotspot.routes];
      });
      setPossibleHotspots(possibleTargets);
      const hotspotArray = createHotspotArray();
      possibleTargets.forEach((el) => {
        hotspotArray[el].classList.add('clickable-hotspot');
        toggleClickableCore();
      });
      hotspotArray.forEach((el, i) => {
        if (!possibleTargets.includes(i)) {
          el.classList.remove('clickable-hotspot');
          toggleClickableCore();
        }
      });
      if (currentPlayer.remainingActions === 0) {
        hotspotArray.forEach((el) => {
          el.classList.remove('clickable-hotspot');
          toggleClickableCore();
        });
      }
      if (currentPlayer.remainingActions === 0 && !disruptionDrawn && !tutorial && !bets) {
        document.querySelector('.card-deck-display_disruption-deck')?.classList.add('card-deck-click');
      }
    }
  }, [players]);

  useEffect(() => {
    const currentPlayer = getCurrentPlayer(players, activeSeat, user);
    if (currentPlayer) {
      if (currentPlayer.remainingActions === 0 && (disruptionDrawn || bets) && !fundingCollected) {
        document.querySelector('.money-move')?.classList.add('money-mover');
      }
    }
  }, [players, boardState]);

  useEffect(() => {
    if (grandAnswers === rooms.length) {
      if (grandRaised >= grandAmount) {
        theGrandAllianceFinal(true);
        !isMute && playGrandalliance();
      } else {
        theGrandAllianceFinal(false);
      }
    }
  }, [grandAnswers]);

  useEffect(() => {
    // zillians
    shields.forEach((shield) => {
      if (shield.zillians > 0) {
        for (let i = 1; i <= shield.zillians; i++) {
          const zilOnShield = document.querySelector(`.zil-${shield.name}.zil${i}`);
          zilOnShield && zilOnShield.classList.remove('display-off');
        }
      }
    });
    // Core
    const coreClimmies = hotspots.find((hotspot) => hotspot.position === 0)?.climmie;
    if (coreClimmies !== undefined) {
      const coreHtml = Array.from(document.querySelectorAll(`.climmie-on-core`));
      coreHtml?.forEach((coreSpot, i) => {
        if (i + 1 <= coreClimmies) {
          coreSpot.classList.remove('display-off');
        } else {
          coreSpot.classList.add('display-off');
        }
      });
    }
    const earnClimmies = document.querySelectorAll('.climmie-earn');
    earnClimmies.forEach((cl, i) => {
      if (i + 1 <= climmies) {
        cl.classList.remove('display-off');
      } else {
        cl.classList.add('display-off');
      }
    });
  }, [boardState]);

  useEffect(() => {
    if (pathwayTokens.length === 5 && !wonGame) {
      //cookie placeholder
      !isMute && playWongame();
      dispatch({ type: 'WON' });
    }
  }, [pathwayTokens]);

  useEffect(() => {
    if (!tutorial && globalEmission) emissionForAll();
  }, [globalEmission]);

  useEffect(() => {
    if (toBeDrawn) {
      document.querySelector('.card-deck-display_disruption-deck')?.classList.add('card-deck-click');
      setDisruptionDrawn(false);
    }
  }, [toBeDrawn]);

  useEffect(() => {
    // zillians
    shields.forEach((shield) => {
      if (shield.zillians > 0) {
        for (let i = 1; i <= shield.zillians; i++) {
          const zilOnShield = document.querySelector(`.zil-${shield.name}.zil${i}`);
          zilOnShield && zilOnShield.classList.remove('display-off');
        }
      }
    });
    // Core
    const coreClimmies = hotspots.find((hotspot) => hotspot.position === 0)?.climmie;
    if (coreClimmies !== undefined) {
      const coreHtml = Array.from(document.querySelectorAll(`.climmie-on-core`));
      coreHtml?.forEach((coreSpot, i) => {
        if (i + 1 <= coreClimmies) {
          coreSpot.classList.remove('display-off');
        } else {
          coreSpot.classList.add('display-off');
        }
      });
    }
    // Earn climmies
    const earnClimmies = document.querySelectorAll('.climmie-earn');
    earnClimmies.forEach((cl, i) => {
      if (i + 1 <= climmies) {
        cl.classList.remove('display-off');
      } else {
        cl.classList.add('display-off');
      }
    });
  }, [boardState]);

  const muteHandler = () => {
    setIsMute(!isMute);
  };

  const getCurrentShieldStatus = (): boolean => {
    const currentPosition = getCurrentPosition(players, activeSeat);
    const currentShield = shields.find((shield) => shield.positions.includes(currentPosition));
    if (currentShield && currentShield.zillians > 2) return true;
    return false;
  };

  const addPathwayToken = (pathway: string): void => {
    if (!pathwayTokens.includes(pathway) && pathwayTokens.length < 5) {
      !isMute && playPathway();
      moveShield(pathway);
      dispatch({ type: 'PATH', path: pathway });
    }
  };

  const movePlayer = (target: number): void => {
    const currentPlayer = getCurrentPlayer(players, activeSeat, user);
    if ((validActionCheck(players, activeSeat, user) || max) && currentPlayer) {
      const currentPosition = currentPlayer.position;
      const possibleTargets = hotspots.find((hotspot) => hotspot.position === currentPosition);
      if (possibleTargets && possibleTargets.routes.includes(target)) {
        const newArray = players.map((player) => {
          if (player.seat === activeSeat) {
            player.position = target;
            if (!max) player.remainingActions -= 1;
          }
          return player;
        });
        dispatchPlayers({ type: 'UPDATE_PLAYERS', playersArray: newArray });
        !isMute && playMove();
        resetEventListenersAndClasses();
        if (getCurrentShieldStatus() && climmieRemovalIsPossible()) removeClimmieFromHotspot();
        if (currentPlayer?.role?.name === 'Max' && !max) dispatchSpecials({ type: 'MAX', setTo: true });
        else dispatchSpecials({ type: 'MAX', setTo: false });
      }
    }
  };

  const drawDisruptionCard = (): void => {
    if (!disruptionDrawn && !bets) {
      if (getCurrentPlayer(players, activeSeat, user)?.remainingActions === 0 && !selectedCard) {
        let copyDisruptionDeck: number[];
        if (disruptionDeck.length > 0) {
          copyDisruptionDeck = disruptionDeck.slice();
        } else {
          disruptionsEscalate();
          copyDisruptionDeck = shuffle(discardedDisruptionCards.slice());
          dispatch({ type: 'DISCARDED_DISR', deck: [] });
        }
        const drawnCard = copyDisruptionDeck.shift();
        console.log('copydisuptionDeck and discarded', copyDisruptionDeck, discardedDisruptionCards);
        dispatch({ type: 'DDECK', deck: copyDisruptionDeck });
        drawnCard && playDisruptionCard(drawnCard);
        setSelectedCard(drawnCard);
        setDisruptionDrawn(true);
        // just for looks
        document.querySelector('.card-deck-display_disruption-deck')?.classList.remove('card-deck-click');
      }
    }
  };

  const removeSelectedCard = (): void => {
    document.querySelector('.selected-card-greyed-out')?.classList.add('display-off');
    Array.from(document.querySelectorAll('.selected-card')).forEach((el) => {
      el?.classList.remove('selected-card');
    });
    document.querySelector('.play-selected-card-btn')?.classList.add('display-off');
    setSelectedCard(undefined);
  };

  const playDisruptionCard = (id: number): void => {
    let shield = 'Wurst';
    const drawnCard = getCardFromId(id);
    dispatch({ type: 'DISCARDED_DISR', deck: [...discardedDisruptionCards, id] });
    if (drawnCard && drawnCard.effects === 'climmie') {
      if (id === 51) {
        const currentPlayerPosition = getCurrentPlayer(players, activeSeat, user)?.position;
        const currentPlayerPositionPathway = hotspots.find((hotspots) => hotspots.position === currentPlayerPosition)
          ?.pathway;
        if (currentPlayerPositionPathway) {
          shield = currentPlayerPositionPathway;
        }
      } else {
        shield = drawnCard.toShield;
      }
      addClimmie(shield);
      if (isDisruptionCard(drawnCard) && drawnCard.globalEffect) {
        drawnCard.id === 44 ? fireDisruption(true, user, isMute) : fireDisruption(false, user, isMute);
      }
      if (toBeDrawn) setToBeDrawn(false);
    } else {
      addCarbion();
      setToBeDrawn(true);
    }
  };

  const disruptionsEscalate = (): void => {
    !isMute && playEscalate();
    setAnnouncement(
      'Your disruption cards ran out and were reshuffled. The repeated disruptions poke more holes in your work/life. An additional Climmie is added to the core in each disruption phase going forward. Remember, the coolest gadget is our resilience. Keep activating Zillians!'
    );
    dispatch({ type: 'ESCALATION' });
  };

  const addClimmie = (shieldName: string): void => {
    let numOfClimmiesToAdd = 0;
    if (carbions < 6) numOfClimmiesToAdd = 1;
    if (carbions > 5 && carbions < 11) numOfClimmiesToAdd = 2;
    if (carbions > 10) numOfClimmiesToAdd = 3;
    const hotspotPositions = shields.find((shield) => shield.name === shieldName)?.positions;
    const newArray: Hotspot[] = [];
    [...hotspots]
      .sort((a, b) => a.position - b.position)
      .forEach((hotspot) => {
        if (hotspotPositions?.includes(hotspot.position)) {
          if (hotspot.climmie === 0 && numOfClimmiesToAdd > 0 && climmies > 0) {
            hotspot.climmie += 1;
            dispatch({ type: 'CLIMMIE', amount: -1 });
            const climmieToHide = document.querySelector(`.climmie-earn${climmies}`);
            if (climmieToHide) {
              climmieToHide.classList.add('display-off');
            }
            numOfClimmiesToAdd--;
          }
        }
        newArray.push(hotspot);
      });
    numOfClimmiesToAdd > 0 ? addClimmieToCore(numOfClimmiesToAdd) : null;
    dispatch({ type: 'HOTSPOT', hotspot: [...newArray] });
    isMute ? null : playClimmie();
  };

  const addClimmieToCore = (numOfClimmiesToAdd: number): void => {
    numOfClimmiesToAdd += escalation;
    let core = hotspots.find((hotspot) => hotspot.position === 0);
    while (numOfClimmiesToAdd > 0) {
      core = hotspots.find((hotspot) => hotspot.position === 0);
      if (core && core.climmie < 3 && climmies > 0) {
        core.climmie++;
        if (core.climmie === 3) {
          requestBailout();
        }
      }
      numOfClimmiesToAdd--;
    }
    const filteredHotspots = hotspots.map((hotspot) => {
      if (core && hotspot.position === 0) {
        hotspot.climmie = core.climmie;
      }
      return hotspot;
    });
    dispatch({ type: 'HOTSPOT', hotspot: filteredHotspots });
    !isMute && playCore();
  };

  const requestBailout = (): void => {
    const newArray = hotspots.map((hotspot) => {
      if (hotspot.position === 0) {
        hotspot.climmie = 0;
      }
      return hotspot;
    });
    !isMute && playBailout();
    dispatch({ type: 'HOTSPOT', hotspot: newArray });
    dispatch({ type: 'SCORE', amount: -3 });
    setAnnouncement(
      'Oh ney, a third climmie reached your core. You got a bailout from the government. 3 score points will be deducted from your final score.'
    );
  };

  const addCarbion = (): void => {
    !isMute && playCarbion();
    dispatch({ type: 'CARBION', amount: 1 });
    showCarbionDialogBox();
  };

  const drawInitiativeCard = (): void => {
    const currentPlayer = getCurrentPlayer(players, activeSeat, user);
    if (currentPlayer && currentPlayer.hand.length < 3) {
      let copyInitiativeDeck;
      if (initiativeDeck.length > 0) {
        copyInitiativeDeck = initiativeDeck.slice();
      } else {
        copyInitiativeDeck = shuffle(discardedInitiativeCards.slice());
        dispatch({ type: 'DISCARDED_INIT', deck: [] });
      }
      const drawnCard = copyInitiativeDeck.pop();
      dispatch({ type: 'IDECK', deck: copyInitiativeDeck });
      const newArray = players.map((player) => {
        if (player.seat === activeSeat) {
          drawnCard && player.hand.push(drawnCard);
        }
        return player;
      });
      dispatchPlayers({ type: 'UPDATE_PLAYERS', playersArray: newArray });
      !isMute && playPickcard();
      discardedCard &&
        document.querySelector('.card-deck-display_initiative-deck')?.classList.remove('card-deck-click');
    }
  };

  const isItPossibleToPlayThisCard = (id: number): boolean => {
    if (id && getCardFromId(id)) {
      const card = getCardFromId(id);
      const currentPlayer = getCurrentPlayer(players, activeSeat, user);
      if (card && isInitiativeCard(card)) {
        if (id > 32) {
          if (id < 35 && !currentPlayer) {
            return false;
          }
          return true;
        }
        if (currentPlayer) {
          let cost = card.cost;
          if (againId > 0) return true;
          if (currentPlayer.remainingActions > 0) {
            if (cost === 0) return true;
            if (currentPlayer.role?.name === 'Chris') {
              if (!chrisEffectFired && initiativesTaken > 0) cost -= 1;
            }
            if (cost <= coins) return true;
          }
        }
      }
    }
    return false;
  };

  const playSelectedCard = (): void => {
    !isMute && playClicksound();
    const player = getCurrentPlayer(players, activeSeat, user);
    let freeToPlay = false;
    if (selectedCard) {
      removeSelectedCard();
      const card = getCardFromId(selectedCard);
      if (selectedCard === againId) freeToPlay = true;
      if (selectedCard > 32) {
        freeToPlay = true;
        playSpecialCard(selectedCard);
      } else if (player && card && isInitiativeCard(card)) {
        if (card.toShield === 'ofChoice') chooseShield();
        if (card.effects === 'add') addZillian(card.toShield, false);
        if (card.effects === 'remove') removeCarbion();
        let cost = card.cost;
        if (!again && !chrisEffectFired && initiativesTaken >= 1 && cost > 0) {
          cost -= 1;
          dispatchSpecials({ type: 'CHRIS', setTo: true });
        }
        !again && dispatch({ type: 'COIN', amount: -cost });
      }
      if (!again && player) {
        const handIndex = player.hand.indexOf(selectedCard);
        const newArray = players.map((player: Player) => {
          if (player.seat === activeSeat) player.hand.splice(handIndex, 1);
          return player;
        });
        !freeToPlay && dispatchPlayers({ type: 'REFRESH_ACTIONS', current: player, actions: -1 });
        dispatchPlayers({ type: 'UPDATE_PLAYERS', playersArray: newArray });
        dispatch({ type: 'DISCARDED_INIT', deck: [...discardedInitiativeCards, selectedCard] });
        setSelectedCard(undefined);
      } else if (card && card.id === 37) {
        const newArray = players.map((player: Player) => {
          if (player.seat === user.seat) {
            const handIndex = player.hand.indexOf(37);
            player.hand.splice(handIndex, 1);
          }
          return player;
        });
        dispatchPlayers({ type: 'UPDATE_PLAYERS', playersArray: newArray });
        dispatch({ type: 'DISCARDED_INIT', deck: [...discardedInitiativeCards, 37] });
        setSelectedCard(undefined);
      } else {
        dispatchSpecials({ type: 'AGAIN_ID', id: 0 });
        dispatchSpecials({ type: 'AGAIN', setTo: false });
      }
      if (player?.role?.name === 'Chris') dispatchSpecials({ type: 'INITIATIVES_TAKEN' });
    }
  };

  const chooseShield = (number = 1): void => {
    document.querySelector('.choose')?.classList.add('greyed-out-shield');
    const shieldArray = Array.from(document.querySelectorAll('.shield'));
    shieldArray.forEach((shield) => shield.classList.add('shield-highlight'));
    shieldArray.forEach((shield) =>
      shield.addEventListener('click', () => addZillian(shield.classList[2], true, number))
    );
  };

  const addZillian = (shieldName: string, choose: boolean, number = 1): void => {
    let x = number;
    if (getCurrentPlayer(players, activeSeat, user)?.role?.name === 'Robin' && areOtherPlayersOnSamePosition())
      x = x + 1;
    choose ? resetShieldListenersAndClasses() : null;
    const filteredShields = shields.map((shield) => {
      if (shield.name === shieldName && shield.zillians + x <= 5) {
        shield.zillians += x;
        if (shield.zillians >= 3 && !pathwayTokens.includes(shieldName)) {
          addPathwayToken(shieldName);
        }
        dispatch({ type: 'SCORE', amount: x });
      }
      return shield;
    });
    dispatch({ type: 'SHIELD', shields: filteredShields });
    !isMute && playZillian();
  };

  const removeCarbion = (): void => {
    !isMute && playRemovecarbion();
    if (carbions > 0) dispatch({ type: 'CARBION', amount: -1 });
    showCarbionDialogBox();
  };

  const showCarbionDialogBox = (): void => {
    const carbionDialogBox = document.querySelector('.board-message-container-carbion');
    if (carbionDialogBox) {
      carbionDialogBox.classList.remove('display-off');
      setTimeout(() => carbionDialogBox.classList.add('display-off'), 10000);
    }
  };

  const refillHand = (player: Player, InitiativeDeck: number[]): Player => {
    const numOfCardsToDraw = 3 - player.hand.length;
    for (let i = 0; i < numOfCardsToDraw; i++) {
      const drawnCard = InitiativeDeck.pop();
      drawnCard && player.hand.push(drawnCard);
    }
    return player;
  };

  const refillAllHands = (): void => {
    let copyInitiativeDeck: number[];
    if (initiativeDeck.length > totalCardsMissing(players)) {
      copyInitiativeDeck = initiativeDeck.slice();
    } else {
      if (initiativeDeck.length > 0) {
        copyInitiativeDeck = initiativeDeck.slice().concat(shuffle(discardedInitiativeCards.slice()));
      } else copyInitiativeDeck = shuffle(discardedInitiativeCards.slice());
      dispatch({ type: 'DISCARDED_INIT', deck: [] });
    }
    const updatedPlayers: Player[] = players.map((player: Player) => {
      if (player.hand.length < 3) {
        refillHand(player, copyInitiativeDeck);
      }
      return player;
    });
    dispatch({ type: 'IDECK', deck: copyInitiativeDeck });
    dispatchPlayers({ type: 'UPDATE_PLAYERS', playersArray: updatedPlayers });
  };

  const discardOneInitiativeCard = (id: number): void => {
    const currentPlayer = getCurrentPlayer(players, activeSeat, user);
    dispatch({ type: 'DISCARDED_INIT', deck: [...discardedInitiativeCards, id] });
    if (currentPlayer && currentPlayer.remainingActions === 0) {
      const handIndex = currentPlayer.hand.indexOf(id);
      const newArray = players.map((player: Player) => {
        if (player.seat === activeSeat) {
          player.hand.splice(handIndex, 1);
        }
        return player;
      });
      dispatchPlayers({ type: 'UPDATE_PLAYERS', playersArray: newArray });
      setSelectedCard(undefined);
      !isMute && playDropcard();
      setDiscardedCard(true);
    }
  };

  const endTurn = (): void => {
    if (getCurrentPlayer(players, activeSeat, user)?.role?.name === 'Chris') {
      dispatchSpecials({ type: 'RESET_INITIATIVES_TAKEN' });
      dispatchSpecials({ type: 'CHRIS', setTo: false });
    }
    dispatchSpecials({ type: 'AGAIN_ID', id: 0 });
    dispatchSpecials({ type: 'BETS', setTo: false });
    dispatchSpecials({ type: 'CRYSTALS', array: [] });
    resetEventListenersAndClasses();
    setDisruptionDrawn(false);
    setDiscardedCard(false);
    setFundingCollected(false);
    !isMute && playEndturn();
    switchToNextPlayer();
  };

  const areOtherPlayersOnSamePosition = (): boolean => {
    const currentPlayer = getCurrentPlayer(players, activeSeat, user);
    const playersExceptMe = players
      .filter((player) => player.seat !== getCurrentPlayer(players, activeSeat, user)?.seat)
      .map((player) => player.position);
    return currentPlayer && playersExceptMe.includes(currentPlayer.position) ? true : false;
  };

  const collectFundingTokens = (): void => {
    const currentPlayer = getCurrentPlayer(players, activeSeat, user);
    if (currentPlayer) {
      if (currentPlayer?.role?.name === 'Jamie' && areOtherPlayersOnSamePosition()) {
        dispatch({ type: 'COIN', amount: climmies > 14 ? 4 : climmies > 9 ? 3 : 2 });
      } else dispatch({ type: 'COIN', amount: climmies > 14 ? 3 : climmies > 9 ? 2 : 1 });
      !isMute && playCoin();
    }
  };

  const removeClimmieFromHotspot = () => {
    if (climmieRemovalIsPossible()) {
      const currentPosition = getCurrentPosition(players, activeSeat);
      const currentHotspot = hotspots.find((hotspot) => hotspot.position === currentPosition);
      if (currentHotspot) {
        currentHotspot.climmie = 0;
        const boardSpot = document.querySelector(`.board-element.spot${currentHotspot.position}`);
        const hiddenClimmie = document.querySelector(`.climmie-earn${climmies + 1}`);
        if (boardSpot && hiddenClimmie) {
          boardSpot.classList.remove('climmie-on-hotspot');
          hiddenClimmie.classList.remove('display-off');
        }
        const filteredHotspots = hotspots.filter((hotspot) => hotspot !== currentHotspot);
        dispatch({ type: 'HOTSPOT', hotspot: [...filteredHotspots, currentHotspot] });
        dispatch({ type: 'CLIMMIE', amount: 1 });
        !isMute && playRemoveclimmie();
        if (!getCurrentShieldStatus()) {
          const currentPlayer = getCurrentPlayer(players, activeSeat, user);
          if (currentPlayer) {
            dispatchPlayers({ type: 'REFRESH_ACTIONS', current: currentPlayer, actions: -1 });
          }
        }
      }
    }
  };

  const climmieRemovalIsPossible = () => {
    const currentPosition = getCurrentPosition(players, activeSeat);
    if (currentPosition === 0) return false;
    const currentHotspot = hotspots.find((hotspot) => hotspot.position === currentPosition);
    if (currentHotspot && currentHotspot.climmie === 1) return true;
    return false;
  };

  const showClimmieRemoveButton = () => {
    const currentPlayer = getCurrentPlayer(players, activeSeat, user);
    if (currentPlayer && climmieRemovalIsPossible() && currentPlayer.remainingActions > 0) return true;
    return false;
  };

  const switchToNextPlayer = (): void => {
    const nextPlayerSeat = findNextPlayer();
    const nextPlayer = getSeatPlayer(nextPlayerSeat, players);
    const updState = boardState;
    updState.activeSeat = nextPlayerSeat;
    const currentPlayer = getCurrentPlayer(players, activeSeat, user);
    if (currentPlayer) {
      dispatchPlayers({ type: 'REFRESH_ACTIONS', current: nextPlayer, actions: 4 });
      dispatch({ type: 'SEAT', seat: nextPlayerSeat });

      if (user.designatedRoom !== undefined) {
        const room = user.designatedRoom;
        updateStateInRoom(updState, room);

        const updPlayers = players.map((player) => {
          if (player.seat === nextPlayerSeat) {
            player.remainingActions = 4;
          }
          return player;
        });
        updatePlayersInRoom(updPlayers, room);
      }
      dispatch({ type: 'NEXT_ROUND' });
    }
  };

  const findNextPlayer = (): number => {
    let nextPlayer;
    if (activeSeat === players.length - 1) {
      nextPlayer = 0;
    } else {
      nextPlayer = activeSeat + 1;
    }
    return nextPlayer;
  };

  const fundingAndCards = (): void => {
    if (user.seat === activeSeat) {
      setSelectedCard(undefined);
      collectFundingTokens();
      refillAllHands();
      setFundingCollected(true);
      document.querySelector('.money-move')?.classList.remove('money-mover');
    }
  };

  const emissionForAll = (): void => {
    !isMute && playEmission();
    setTimeout(() => {
      addCarbion();
    }, 14000);
    setGlobalEmission(false);
  };

  const assignRoles = (): void => {
    const newArray = players.map((player, i) => {
      const id = boardState.roleDeck[i];
      const role = getRoleFromId(id);
      player.role = role;
      dispatch({ type: 'ROLES_POP' });
      return player;
    });
    dispatchPlayers({ type: 'UPDATE_PLAYERS', playersArray: newArray });
  };
  // ~~~~ SPECIAL CARDS ~~~~
  const playSpecialCard = (id: number): void => {
    switch (id) {
      case 33:
        dispatchSpecials({ type: 'AGAIN', setTo: true });
        return;
      case 34:
        console.log('sharing is caring special');
        return;
      case 35:
        triggerGrandAlliance();
        return;
      case 36:
        setSelectPlayer(true);
        return;
      case 37:
        dispatchSpecials({ type: 'CRYSTAL', setTo: true });
        return;
      case 38:
        dispatchSpecials({ type: 'BETS', setTo: true });
        return;
      default:
        console.log('Something went wrong in playSpecialCard');
    }
  };

  // Global disruption card functions
  const fireDisruption = (left: boolean, user: User, isMute: boolean): void => {
    const room = user.designatedRoom;
    !isMute && playOhhwee();
    if (room !== undefined && rooms.length > 1) globalDisruptionTrigger(room, left);
  };

  const chooseDisruptionPlace = (hotspot: number) => {
    const toRoom = globalChoice.from;
    if (toRoom !== undefined && user.seat === activeSeat) {
      globalDisruptionChoice(hotspot, toRoom);
    }
    // resetEventListenersAndClasses();
    setGlobalChoice({ to: null });
  };

  // SAVE AND LOAD TO/FROM DATABASE
  const saveGame = async () => {
    const savedBoard = await updateBoard({
      boardData: JSON.stringify(boardState),
      room: user.designatedRoom,
      players: JSON.stringify(players),
      boardId: user.boardId,
    });
    console.log('Successfully saved this game: ', savedBoard);
  };

  const loadGame = async () => {
    const loadedBoard = await loadBoard(user.boardId);
    console.log('Successfully loaded this game: ', loadedBoard);
  };

  const selectPlayerToMove = (id: number): void => {
    setChosenPlayer(id);
    setSelectPlayer(false);
  };

  const sendAnswer = (yes: boolean) => {
    const room = user.designatedRoom;
    room !== undefined && quick && sendPermissionToMove(user.seat, yes, quick, room);
    dispatchSpecials({ type: 'QUICK', id: -1 });
  };

  const handleHotspotClick = (e: MouseEvent<HTMLDivElement>) => {
    const id = parseInt(e.currentTarget.id);
    if (e.currentTarget.classList.contains('clickable-hotspot')) {
      movePlayer(id);
    } else if (e.currentTarget.classList.contains('send-climmie-hotspot')) {
      chooseDisruptionPlace(id);
    } else if (e.currentTarget.classList.contains('move-teammate-to-hotspot')) {
      const room = user.designatedRoom;
      room !== undefined && askPermissionToMove(user.seat, chosenPlayer, id, user.designatedRoom);
      setChosenPlayer(-1);
    }
  };

  const triggerGrandAlliance = () => {
    const room = user.designatedRoom;
    const numRooms = rooms.length;
    const amount = numRooms * 3 + 1;
    room !== undefined && theGrandAllianceTrigger(room, amount);
  };

  const sendGrandAllianceOffer = (offer: number) => {
    if (offer <= coins) {
      theGrandAllianceOffer(offer, grand);
      dispatchSpecials({ type: 'GRAND_OFFER', id: offer });
      if (user.seat === activeSeat && user.designatedRoom === grand) {
        dispatchSpecials({ type: 'GRAND_RAISED', id: +offer });
      }
    }
    dispatchSpecials({ type: 'GRAND_ANSWERS', id: 1 });
    dispatchSpecials({ type: 'GRAND', id: -1 });
  };

  const resetGrandAllianceStates = (): void => {
    dispatchSpecials({ type: 'GRAND_SUCCESS', id: 0 });
    dispatchSpecials({ type: 'GRAND_RAISED', id: -grandRaised });
    dispatchSpecials({ type: 'GRAND_ANSWERS', id: -grandAnswers });
    dispatchSpecials({ type: 'GRAND_OFFER', id: 0 });
    dispatchSpecials({ type: 'GRAND_AMOUNT', id: -1 });
  };

  const grandAllianceSelectShield = (): void => {
    dispatch({ type: 'COIN', amount: -grandOffer });
    resetGrandAllianceStates();
    user.seat === activeSeat && chooseShield(3);
  };

  const tutorialHighlighter = (className: string): void => {
    Array.from(document.querySelectorAll(className)).forEach((element) => element.classList.add('tut'));
  };
  const tutorialReseter = (className: string): void => {
    Array.from(document.querySelectorAll(className)).forEach((element) => element.classList.remove('tut'));
  };
  const tutorialGreyOut = (className: string): void => {
    Array.from(document.querySelectorAll(className)).forEach((element) => element.classList.add('greyed-out'));
  };
  const tutorialRemoveGreyOut = (className: string): void => {
    Array.from(document.querySelectorAll(className)).forEach((element) => element.classList.remove('greyed-out'));
  };
  const tutorialHand = (): void => {
    document.querySelector('.players-hand')?.classList.add('tut-hand');
  };
  const tutorialHandRemove = (): void => {
    document.querySelector('.players-hand')?.classList.remove('tut-hand');
  };

  return (
    <>
      {/* <button onClick={endTurn}>end turn</button> */}
      {/* DATABASE  */}
      {/* <button onClick={saveGame}>Save Game</button>
      <button onClick={loadGame}>Load Games</button> */}
      <div className="game">
        {tutorial ? (
          <Tutorial
            tutorialReseter={tutorialReseter}
            tutorialHighlighter={tutorialHighlighter}
            isMute={isMute}
            playEmission={playEmission}
            dispatch={dispatch}
            tutorialGreyOut={tutorialGreyOut}
            tutorialRemoveGreyOut={tutorialRemoveGreyOut}
            tutorialHandRemove={tutorialHandRemove}
            tutorialHand={tutorialHand}
            socket={socket}
            activeSeat={activeSeat}
            updatePlayersInRoom={updatePlayersInRoom}
            players={players}
            user={user}
            myRole={myRole}
            setTutorialRole={setTutorialRole}
          />
        ) : null}
        <div className="board-element initiative-cards"></div>
        <div className="board-element disruption-cards"></div>
        <OtherPlayersCardsDisplay setOtherPlayer={setOtherPlayer} player={otherPlayer} user={user} />
        <PlayerDisplay
          score={score}
          players={players}
          activeSeat={activeSeat}
          user={user}
          setOtherPlayer={setOtherPlayer}
          tutorialRole={tutorialRole}
          rooms={rooms}
          muteHandler={muteHandler}
          isMute={isMute}
          setReferenceDisplay={setReferenceDisplay}
          referenceDisplay={referenceDisplay}
        />
        {/* BOARD */}
        <div className="board">
          {tutorial ? <div className="tut-board greyed-out"></div> : null}
          <div className="board-background"></div>
          {/* <div className="paths"></div> */}
          <div className="elements-grid">
            {players.map((player) => (
              <PlayerPawn
                actions={player.remainingActions}
                activeSeat={activeSeat}
                key={player.name}
                seat={player.seat}
                position={player.position}
                color={player.role ? player.role.color : 'black'}
              />
            ))}
            <DisruptionCloud carbions={carbions} />
            <div className="board-message-container-carbion display-off">
              <div className="board-dialog-box-carbion">
                <p className="board-dialog-box-carbion-text">
                  One carbion was added into the cloud. You now have <span>{carbions}</span>.
                  <br />
                  {carbions < 5
                    ? `Carbions till next stage: ${6 - carbions}.`
                    : carbions < 11
                    ? `Carbions till next stage: ${11 - carbions}.`
                    : "The cloud won't get any larger."}
                </p>
              </div>
              <div className="board-dialog-box-pointer-carbion"></div>
            </div>

            <div className="board-element earning-level">
              <div className="climmie-earn climmie-earn1"></div>
              <div className="climmie-earn climmie-earn2"></div>
              <div className="climmie-earn climmie-earn3"></div>
              <div className="climmie-earn climmie-earn4"></div>
              <div className="climmie-earn climmie-earn5"></div>
              <div className="climmie-earn climmie-earn6"></div>
              <div className="climmie-earn climmie-earn7"></div>
              {/* Hidden at the beginning of the game */}
              <div className="climmie-earn display-off climmie-earn8"></div>
              <div className="climmie-earn display-off climmie-earn9"></div>
              <div className="climmie-earn display-off climmie-earn10"></div>
              <div className="climmie-earn display-off climmie-earn11"></div>
              <div className="climmie-earn display-off climmie-earn12"></div>
              <div className="climmie-earn display-off climmie-earn13"></div>
              <div className="climmie-earn display-off climmie-earn14"></div>
              <div className="climmie-earn display-off climmie-earn15"></div>
              <div className="climmie-earn display-off climmie-earn16"></div>
              <div className="climmie-earn display-off climmie-earn17"></div>
              <div className="climmie-earn display-off climmie-earn18"></div>
              <div className="climmie-earn display-off climmie-earn19"></div>
            </div>
            <div className="money-light"></div>
            {getCurrentPlayer(players, activeSeat, user) &&
            getCurrentPlayer(players, activeSeat, user)?.remainingActions === 0 &&
            (disruptionDrawn || bets) &&
            !fundingCollected ? (
              <div onClick={fundingAndCards} className="click-money board-element money" />
            ) : null}
            <div className="board-element money money-move">
              <div className="funding">{coins}</div>
            </div>
            <div className="board-element victory-track">
              {pathwayTokens.map((token, i) => {
                const animationDiv = document.querySelector('.shield-animation-div');
                animationDiv ? animationDiv.classList.add(`${token}`) : null;
                return (
                  <div key={i} className={`shield-icons shield-icon${i + 1}`}>
                    <img src={require(`../../assets/shields/p-${token}.png`).default}></img>
                  </div>
                );
              })}
            </div>
            <div className="shield-animation-div"></div>
            <div className="board-element minion zillian"></div>
            <div
              className="board-element minion carbion"
              onMouseEnter={onMouseEnterCarbion}
              onMouseLeave={onMouseLeaveCarbion}
            ></div>
            <div className="board-element minion climmie"></div>
            {hotspots.map((hotspot) => {
              if (hotspot.climmie) {
                if (!document.querySelector(`.spot${hotspot.position}`)?.attributes[0].value.includes('core')) {
                  document
                    .querySelector(`.board-element.spot.spot${hotspot.position}`)
                    ?.classList.add('climmie-on-hotspot');
                  document
                    .querySelector(`.board-element.start.spot${hotspot.position}`)
                    ?.classList.add('climmie-on-hotspot');
                }
              }
            })}
            {/* Shields */}
            <div className="choose"></div>
            {boardState.shields.map((shield) => {
              return (
                <div key={shield.name} className={`board-element shield ${shield.name}`}>
                  <ShieldSVG key={shield.name} user={user} shieldType={shield.name} />
                  <div className={`zil-on-shield zil-${shield.name} zil1 display-off`}></div>
                  <div className={`zil-on-shield zil-${shield.name} zil2 display-off`}></div>
                  <div className={`zil-on-shield zil-${shield.name} zil3 display-off`}></div>
                  <div className={`zil-on-shield zil-${shield.name} zil4 display-off`}></div>
                  <div className={`zil-on-shield zil-${shield.name} zil5 display-off`}></div>
                </div>
              );
            })}
            {/* Spots*/}
            <div className="core-highlight spot0 display-off"></div>
            <div onClick={handleHotspotClick} id="0" className="board-element spot0 core">
              <div className={`climmie-on-core climmie1 display-off`}></div>
              <div className={`climmie-on-core climmie2 display-off`}></div>
              <div className={`climmie-on-core climmie3 display-off`}></div>
            </div>
            <div onClick={handleHotspotClick} id="1" className="board-element start spot1"></div>
            <div onClick={handleHotspotClick} id="2" className="board-element spot spot2"></div>
            <div onClick={handleHotspotClick} id="3" className="board-element spot spot3"></div>
            <div onClick={handleHotspotClick} id="4" className="board-element spot spot4"></div>
            <div onClick={handleHotspotClick} id="5" className="board-element spot spot5"></div>
            <div onClick={handleHotspotClick} id="6" className="board-element spot spot6"></div>
            <div onClick={handleHotspotClick} id="7" className="board-element spot spot7"></div>
            <div onClick={handleHotspotClick} id="8" className="board-element spot spot8"></div>
            <div onClick={handleHotspotClick} id="9" className="board-element spot spot9"></div>
            <div onClick={handleHotspotClick} id="10" className="board-element spot spot10"></div>
            <div onClick={handleHotspotClick} id="11" className="board-element spot spot11"></div>
            <div onClick={handleHotspotClick} id="12" className="board-element spot spot12"></div>
            <div onClick={handleHotspotClick} id="13" className="board-element spot spot13"></div>
            <div onClick={handleHotspotClick} id="14" className="board-element spot spot14"></div>
            <div onClick={handleHotspotClick} id="15" className="board-element spot spot15"></div>
            <div onClick={handleHotspotClick} id="16" className="board-element spot spot16"></div>
            {showClimmieRemoveButton() ? (
              <button className="button btn-remove-climmie" onClick={removeClimmieFromHotspot}>
                Remove Climmie
              </button>
            ) : null}
            {fundingCollected ? (
              <button className="end-turn-btn button" onClick={endTurn}>
                End Turn
              </button>
            ) : null}
            <Announcement announcement={announcement} />
          </div>
        </div>
        <PlayersHand
          activeSeat={activeSeat}
          user={user}
          players={players}
          setSelectedCard={setSelectedCard}
          dispatchSpecials={dispatchSpecials}
          again={again}
        />
        {selectedCard ? (
          <SelectedCard
            card={getCardFromId(selectedCard)}
            setSelectedCard={setSelectedCard}
            dispatchSpecials={dispatchSpecials}
            selectedCard={selectedCard}
            isItPossibleToPlayThisCard={isItPossibleToPlayThisCard}
            again={again}
            removeSelectedCard={removeSelectedCard}
            playSelectedCard={playSelectedCard}
            getCurrentPlayer={getCurrentPlayer}
            discardedCard={discardedCard}
            players={players}
            activeSeat={activeSeat}
            user={user}
            discardOneInitiativeCard={discardOneInitiativeCard}
          />
        ) : null}
        <DialogBox
          globalChoice={globalChoice}
          setGlobalChoice={setGlobalChoice}
          sendAnswer={sendAnswer}
          quick={quick}
          selectPlayer={selectPlayer}
          selectPlayerToMove={selectPlayerToMove}
          players={players}
          user={user}
          activeSeat={activeSeat}
          grand={grand}
          grandAmount={grandAmount}
          grandAnswers={grandAnswers}
          sendGrandAllianceOffer={sendGrandAllianceOffer}
          grandSuccess={grandSuccess}
          resetGrandAllianceStates={resetGrandAllianceStates}
          grandAllianceSelectShield={grandAllianceSelectShield}
          coins={coins}
        />
        <CardDecksDisplay
          displayDiscardedCards={displayDiscardedCards}
          drawDisruptionCard={drawDisruptionCard}
          discardedCard={discardedCard}
          drawInitiativeCard={drawInitiativeCard}
        />
        <DiscardedCardsDisplay
          user={user}
          discardedDisruptionCards={discardedDisruptionCards}
          discardedInitiativeCards={discardedInitiativeCards}
          again={again}
          setSelectedCard={setSelectedCard}
          dispatchSpecials={dispatchSpecials}
          hideDiscardedCards={hideDiscardedCards}
        />
        {crystal ? (
          <TopThreeCards
            crystal={crystal}
            crystals={crystals}
            dispatchSpecials={dispatchSpecials}
            disruptionDeck={disruptionDeck}
            dispatch={dispatch}
            user={user}
          />
        ) : null}
        {referenceDisplay ? <ReferenceDisplay setReferenceDisplay={setReferenceDisplay} /> : null}
      </div>
    </>
  );
};

export default Board;
