import React, { useState, useEffect } from 'react';
import { BrowserRouter as Router, Route, Switch } from 'react-router-dom';
import { Room, Player, User } from './Interfaces/Board.types';
import Landing from './components/Landing/Landing';
import Admin from './components/Admin/Admin';
import Startcomponent from './containers/Start.Component/Startcomponent';
import Lobby from './components/Lobby/Lobby';
import Board from './components/Board/Board';
import './App.scss';

import {
  initiateSocket,
  updateRoomListener,
  chatListener,
  joinSocketRoom,
  updatePlayersListener,
  leaveSocketRoom,
  adminChatListener,
  updateGamestatusListener,
  startGameListener,
} from './services/SocketApi';

function App(): JSX.Element {
  // eslint-disable-next-line @typescript-eslint/ban-ts-comment
  //@ts-ignore
  const [socket, setSocket] = useState<SocketIOClient.Socket>(false);
  const [players, setPlayers] = useState<Player[]>([]);
  const [user, setUser] = useState<User>({ name: '', seat: 0, language: 'en' });
  const [rooms, setRooms] = useState<Room[]>([{ id: 1, players: [], _id: '' }]);
  const [gameStarting, setGameStarting] = useState<boolean>(false);
  const [initGame, setInitGame] = useState<boolean>(false);
  const [initPlayers, setInitPlayers] = useState<Player[]>([]);
  const [routing, setRouting] = useState<boolean>(true);

  useEffect(() => {
    if (socket) {
      updatePlayersListener({ players, setPlayers });
      setUser({ ...user });
      chatListener();
      updateRoomListener({ rooms, setRooms });
      adminChatListener();
      updateGamestatusListener({ gameStarting, setGameStarting });
      startGameListener({ setInitGame, setInitPlayers });
    }
  }, [socket]);

  const initSocket = async (name: string): Promise<void> => {
    const tempPlayer = { name: name, seat: 0, language: 'en' };
    setUser(tempPlayer);
    const newSocket = await initiateSocket();
    setSocket(newSocket);
  };

  // May be otimized by reducer or changing argumentlist
  const joinRoom = (roomId: number): void => {
    if (roomId !== user.designatedRoom) {
      if (user.designatedRoom !== undefined) {
        leaveRoom(user.designatedRoom);
      }
      setTimeout(() => {
        const seat = rooms[roomId].players.length;
        const newUser = { ...user, designatedRoom: roomId, seat: seat };
        setUser(newUser);
        joinSocketRoom(newUser, roomId);
      }, 300);
    }
  };

  const leaveRoom = (roomId: number): void => {
    if (user.designatedRoom !== undefined) {
      const newUser = { ...user, designatedRoom: undefined, seat: 0 };
      setUser(newUser);
      leaveSocketRoom(user, roomId);
    }
    // updatePlayers(user);
  };

  return (
    <Router>
      <Switch>
        <Route exact path="/">
          <Landing setSocket={setSocket} initSocket={initSocket} />
        </Route>
        <Route path="/gamestart">
          <Startcomponent initSocket={initSocket} />
        </Route>
        <Route path="/lobby">
          <Lobby
            setUser={setUser}
            user={user}
            players={players}
            rooms={rooms}
            joinRoom={joinRoom}
            leaveRoom={leaveRoom}
            gameStarting={gameStarting}
            initGame={initGame}
          />
        </Route>
        <Route path="/admin">
          <Admin
            initGame={initGame}
            setSocket={setSocket}
            players={players}
            setRooms={setRooms}
            rooms={rooms}
            setGameStarting={setGameStarting}
            gameStarting={gameStarting}
            socket={socket}
          />
        </Route>
        <Route path={`/board/${user.designatedRoom}`}>
          <Board setRouting={setRouting} initPlayers={initPlayers} user={user} socket={socket} rooms={rooms} />
        </Route>
      </Switch>
      {/* : (
        <Board initPlayers={initPlayers} user={user} socket={socket} rooms={rooms} />
      )
      } */}
    </Router>
  );
}

export default App;
