import React, { useState, ChangeEvent, FormEvent, FunctionComponent } from 'react';
import { sendChatMessage } from '../../services/SocketApi';
import { User } from '../../Interfaces/Board.types';
import './ChatBox.scss';
// import Message from '../Message/Message';

interface Props {
  user: User;
  channel?: number;
}

const Chatcomponent: FunctionComponent<Props> = ({ channel, user }: Props) => {
  const [message, setMessage] = useState<string>('');

  const sendMessage = (e: FormEvent): void => {
    e.preventDefault();
    if (user.designatedRoom !== undefined && message !== '') {
      console.log(`user ${user.name} is sending a message to ${user.designatedRoom}`);
      sendChatMessage(user, message, channel);
      // const container = document.getElementById('message');
      // const message = <Message author={user.name} message={message} room={channel} />;
    }
    setMessage('');
  };

  const messageHandler = (e: ChangeEvent<HTMLInputElement>): void => {
    const value = e.target.value;
    setMessage(value);
  };

  return (
    <div className="chat-input-container">
      <form onSubmit={sendMessage} className="chat-input-form">
        <label htmlFor="message"></label>
        <input
          name="message"
          id="message"
          type="text"
          value={message}
          onChange={messageHandler}
          placeholder="Write message..."
        />
        <button type="submit">Send</button>
      </form>
    </div>
  );
};

export default Chatcomponent;
