import DisruptionCard from '../DisruptionCards';
import { Dispatch, FunctionComponent, useState } from 'react';
import React from 'react';
import { IDisruptionCard, SpecialAction, BoardAction, User } from '../../Interfaces/Board.types';
import { getCardFromId, isDisruptionCard } from '../Utils/Helpers';
import './TopThreeCards.scss';

interface Props {
  disruptionDeck: number[];
  crystal: boolean;
  crystals: number[];
  dispatchSpecials: Dispatch<SpecialAction>;
  dispatch: Dispatch<BoardAction>;
  user: User;
}

const TopThreeCards: FunctionComponent<Props> = ({
  dispatch,
  disruptionDeck,
  crystal,
  dispatchSpecials,
  user,
}: Props) => {
  const [selection, setSelection] = useState([5, 5, 5]);
  const idArray = disruptionDeck.slice(0, 3);
  const topThreeArray: IDisruptionCard[] = [];
  idArray.forEach((id) => {
    const card = getCardFromId(id);
    if (card && isDisruptionCard(card)) topThreeArray.push(card);
  });

  const sendSelection = (): void => {
    const selectionToSend = selection.filter((num) => num !== 5);
    const deckCopy = disruptionDeck.slice(selectionToSend.length);
    const sortedCards = switchSequence(idArray, selectionToSend);
    const sortedDeck = sortedCards.concat(deckCopy);
    dispatchSpecials({ type: 'CRYSTAL', setTo: false });
    dispatch({ type: 'DDECK', deck: sortedDeck });
  };
  const reset = (): void => {
    setSelection([5, 5, 5]);
  };

  const selector = (number: number): void => {
    const numOfCardsSelected = selection.filter((num) => num !== 5).length;
    if (selection[number] === 5)
      setSelection((prev) => {
        const newArray = [...prev];
        newArray.splice(number, 1, numOfCardsSelected + 1);
        return newArray;
      });
  };

  const switchSequence = (cards: number[], positions: number[]): number[] => {
    const sortedArray: number[] = [];
    for (let i = 0; i < positions.length; i++) {
      const number = i + 1;
      const indexFinder = positions.indexOf(number);
      sortedArray.push(cards[indexFinder]);
    }
    return sortedArray;
  };

  return (
    <div className="top-three-cards">
      {topThreeArray.map((card, i) => {
        return (
          <div key={i} className="card">
            <DisruptionCard
              selector={selector}
              selection={selection}
              number={i}
              card={card}
              crystal={crystal}
              user={user}
            />
          </div>
        );
      })}
      <button onClick={reset} className="button">
        Reset
      </button>
      <button onClick={sendSelection} className="button">
        Accept
      </button>
    </div>
  );
};
export default TopThreeCards;
