import { Boardsetup, BoardAction } from '../Interfaces/Board.types';
import {
  initShields,
  initInitiativeDeck,
  initDisruptionDeck,
  initHotspots,
  initRoles,
} from '../components/Utils/Intitializers';

const initialBoardSetup: Boardsetup = {
  score: 0,
  escalation: 0,
  carbions: 3,
  climmies: 7,
  coins: 3,
  pathwayTokens: [],
  shields: initShields,
  initiativeDeck: initInitiativeDeck,
  discardedInitiativeCards: [],
  disruptionDeck: initDisruptionDeck,
  discardedDisruptionCards: [],
  hotspots: initHotspots,
  activeSeat: 0,
  roundCount: 1,
  roleDeck: initRoles,
  rolesAssigned: false,
  wonGame: false,
  tutorial: true,
};

const boardReducer = (boardState: Boardsetup, action: BoardAction): Boardsetup => {
  switch (action.type) {
    case 'SCORE':
      return action.amount ? { ...boardState, score: boardState.score + action.amount } : boardState;
    case 'ESCALATION':
      return { ...boardState, escalation: boardState.escalation + 1 };
    case 'CARBION':
      return action.amount ? { ...boardState, carbions: boardState.carbions + action.amount } : boardState;
    case 'CLIMMIE':
      return action.amount ? { ...boardState, climmies: boardState.climmies + action.amount } : boardState;
    case 'COIN':
      return action.amount ? { ...boardState, coins: boardState.coins + action.amount } : boardState;
    case 'PATH':
      let x = 3;
      if (boardState.pathwayTokens.length >= 1) x = 2;
      if (boardState.pathwayTokens.length === 3) x = 1;
      if (boardState.pathwayTokens.length >= 4) x = 0;
      return action.path
        ? { ...boardState, pathwayTokens: [...boardState.pathwayTokens, action.path], coins: boardState.coins + x }
        : boardState;
    case 'SHIELD':
      return action.shields ? { ...boardState, shields: boardState.shields } : boardState;
    case 'IDECK':
      return action.deck ? { ...boardState, initiativeDeck: action.deck } : boardState;
    case 'DDECK':
      return action.deck ? { ...boardState, disruptionDeck: action.deck } : boardState;
    case 'DISCARDED_DISR':
      return action.deck ? { ...boardState, discardedDisruptionCards: action.deck } : boardState;
    case 'DISCARDED_INIT':
      return action.deck ? { ...boardState, discardedInitiativeCards: action.deck } : boardState;
    case 'HOTSPOT':
      return action.hotspot ? { ...boardState, hotspots: action.hotspot } : boardState;
    case 'SEAT':
      return action.seat !== undefined ? { ...boardState, activeSeat: action.seat } : boardState;
    case 'ROLES':
      return action.deck ? { ...boardState, roleDeck: action.deck } : boardState;
    case 'ROLES_POP':
      return { ...boardState, roleDeck: boardState.roleDeck.slice(1) };
    case 'ROLES_ASSIGNED':
      return { ...boardState, rolesAssigned: true };
    case 'GOT_UPDATE':
      return action.board && action.board !== boardState ? { ...action.board } : boardState;
    case 'NEXT_ROUND':
      return { ...boardState, roundCount: boardState.roundCount + 1 };
    case 'WON':
      return { ...boardState, wonGame: true };
    case 'TUTORIAL':
      return { ...boardState, tutorial: false };
    default:
      throw new Error('Something went wrong in board reducer');
  }
};

export { boardReducer, initialBoardSetup };
