import React, { FunctionComponent, useEffect } from 'react';
import './Tutorial.scss';

interface P {
  playEmission: () => void;
  isMute: boolean;
  tutorialHighlighter: (className: string) => void;
}

const GlobalEmissions: FunctionComponent<P> = ({ playEmission, isMute, tutorialHighlighter }: P): JSX.Element => {
  useEffect(() => {
    tutorialHighlighter('.disruption-clouds');
    tutorialHighlighter('.zillian');
    tutorialHighlighter('.carbion');
    !isMute && playEmission();
  }, []);
  return (
    <>
      <div className="dialog-box dialog-box-zillian">
        <div>
          <p className="minion-message">
            Our fossil fuel emission continues to build up greenhouse gases.
            <br />
            <br />
            Every time you hear this sound effect, a carbion is added to the cloud.
            <br />
            <br />
            Watch out for the escalating effect of the disruption scale on the disruption cards. The higher the scale,
            the more Climmies will be added.
          </p>
        </div>
      </div>
      <div className="dialog-box-pointer"></div>
    </>
  );
};

export default GlobalEmissions;
