import React, { ChangeEvent, Dispatch, MouseEvent, SetStateAction, useState, FormEvent } from 'react';
import './DiaglogBox.scss';
import { GlobalChoice, Player, User } from '../../Interfaces/Board.types';

interface Props {
  globalChoice: GlobalChoice;
  setGlobalChoice: Dispatch<SetStateAction<GlobalChoice>>;
  sendAnswer(yes: boolean): void;
  quick: number;
  selectPlayer: boolean;
  players: Player[];
  user: User;
  activeSeat: number;
  selectPlayerToMove(id: number): void;
  grand: number;
  grandAmount: number;
  sendGrandAllianceOffer(offer: number): void;
  grandSuccess: number;
  grandAnswers: number;
  resetGrandAllianceStates(): void;
  grandAllianceSelectShield(): void;
  coins: number;
}

function DialogBox({
  globalChoice,
  setGlobalChoice,
  sendAnswer,
  quick,
  selectPlayer,
  selectPlayerToMove,
  players,
  user,
  grand,
  grandAmount,
  activeSeat,
  sendGrandAllianceOffer,
  grandSuccess,
  grandAnswers,
  resetGrandAllianceStates,
  grandAllianceSelectShield,
  coins,
}: Props): JSX.Element {
  const [offeredAmount, setOfferedAmount] = useState<number>(0);
  let input = '';
  if (globalChoice.dialog) input = globalText;
  if (quick > 0) input = askForPermission;
  if (grand > -1) input = grandAlliance;

  const dialogHandler = (e: MouseEvent<HTMLButtonElement>) => {
    if (globalChoice.dialog) {
      setGlobalChoice({ ...globalChoice, dialog: false });
    }
    if (quick > 0) {
      console.log(e.currentTarget.value);
      e.currentTarget.value === 'yes' ? sendAnswer(true) : sendAnswer(false);
    }
    if (grandSuccess > 0) {
      grandSuccess === 2 ? grandAllianceSelectShield() : resetGrandAllianceStates();
    }
  };

  const selectPlayerHandler = (e: any) => {
    selectPlayerToMove(parseInt(e.target.id));
  };

  const tgaSubmitHandler = (e: FormEvent<HTMLFormElement>): void => {
    e.preventDefault();
    sendGrandAllianceOffer(offeredAmount);
  };

  const tgaChangeHandler = (e: ChangeEvent<HTMLInputElement>): void => {
    setOfferedAmount(parseInt(e.target.value));
  };

  return (
    <>
      {globalChoice.dialog ? (
        <div className="dialog-container">
          <div className="dialog-text">
            Uh wee, you have drawn a disruption card that lets you and your team send a climmie to a hotspot on the
            neighbouring table. The hotspots highighted with red on your board shows the current state of your
            neighbours table.
            <br />
            <br />
            The current player can click any of the red spots to place a climmie in that position on their board. If you
            are not the current player, clicking the red hotspots will have no effect.
          </div>
          <button className="dialog-button button" onClick={dialogHandler}>
            Understood
          </button>
        </div>
      ) : null}
      {quick >= 0 ? (
        <div className="dialog-container">
          <div className="dialog-text">{input}</div>
          <div className="dialog-buttons">
            <button className="dialog-button button" value="yes" onClick={dialogHandler}>
              Yes
            </button>
            <button className="dialog-button button" value="no" onClick={dialogHandler}>
              No
            </button>
          </div>
        </div>
      ) : null}
      {selectPlayer ? (
        <div className="choose-player-container">
          <div style={{ marginTop: '1rem' }}>Choose player:</div>
          <div className="player-picker-container">
            {players.map((player) => {
              if (player.seat !== user.seat) {
                return (
                  <div key={player.seat} className="player-picker" onClick={selectPlayerHandler}>
                    <div>
                      <img
                        id={player.seat.toString()}
                        className="player-display-frame"
                        src={player.role?.image}
                        alt="player image"
                      ></img>
                    </div>
                    <div>{player.name}</div>
                  </div>
                );
              }
            })}
          </div>
        </div>
      ) : null}
      {grandAmount >= 0 && grandAnswers === 0 ? (
        <div className="tga-container">
          <div className="dialog-text">
            One of your neighbouring tables drew the initiative card The Grand Alliance. This is your chance to work
            together! If you collectively can raise enough funding tokens, you will all get the possibility to place 3
            zillians on any shield of choice.
            <br />
            <br />
            The currently active player on your team can use the slider below to change how much you want to contribute
            with, and when you all agree, press SEND OFFER to send it back to your neighbours.
          </div>
          {user.seat === activeSeat && (
            <form className="tga-form" onSubmit={tgaSubmitHandler}>
              <label htmlFor="tga-slider"></label>
              <input
                type="range"
                min="0"
                max={Math.min(coins, grandAmount)}
                value={offeredAmount}
                className="tga-slider"
                name="tga-slider"
                onChange={tgaChangeHandler}
              />
              <div className="amount-text">
                Offered amount: {Math.min(offeredAmount, grandAmount)} / {Math.min(coins, grandAmount)}
                <br />
                Total amount to gether: {grandAmount}
              </div>
              <button type="submit" className="dialog-button">
                Send offer
              </button>
            </form>
          )}
        </div>
      ) : null}
      {grandSuccess === 0 && grandAnswers > 0 ? (
        <div className="tga-waiting-container">
          <div className="tga-waiting-text">Waiting for other teams to submit their offers...</div>
        </div>
      ) : null}
      {grandSuccess === 1 && (
        <div className="dialog-container">
          <div className="dialog-text">Too bad, you were not able to collect enough funds.</div>
          <button className="dialog-button" onClick={dialogHandler}>
            Damn, that sucks...
          </button>
        </div>
      )}
      {grandSuccess === 2 && (
        <div className="dialog-container">
          <div className="dialog-text">
            Great, teamwork makes the dream work! You all came together and raised enough funds. The current player at
            your table can now place 3 zillians at any shield of your choice.
          </div>
          <button className="dialog-button" onClick={dialogHandler}>
            Uuh wee!
          </button>
        </div>
      )}
    </>
  );
}

export default DialogBox;

const globalText =
  'Uh wee, you have drawn a disruption card that lets you and your team send a climmie to a hotspot on the neighbouring table. The hotspots highighted with red on your board shows the current state of your neighbours table. The current player can click any of the red spots to place a climmie in that position on their board. If you are not the current player, clicking the red hotspots will have no effect.';

const askForPermission = 'A teammate has asked permission to move your pawn to another position. Do you accept?';
const grandAlliance =
  'One of your neighbouring tables drew the initiative card "The Grand Alliance. This is your chance to work together! If you collectively can raise enough funding tokens, you will all get the possibility to place 3 zillians on any shield of choice. The currently active player on your team can use the slider below to change how much you want to contribute with, and when you all agree, press "SEND OFFER" to send it back to your neighbours.';
