const BASE_URL = process.env.REACT_APP_BASE_URL;
import { Room } from '../Interfaces/Board.types';

interface ServerTokenData {
  language: string;
  game_version: string;
}

interface BoardResponse {
  boardData: string;
  room: number | undefined;
  players: string;
  boardId: string;
}

interface RoomCreateData {
  tokenId: number;
  roomNumber: number;
}

export const createBoards = (data: RoomCreateData): Promise<Room[]> => {
  console.log('creating rooms with: ', data);
  return fetch(`${BASE_URL}/board`, {
    method: 'POST',
    headers: { 'Content-Type': 'application/json' },
    body: JSON.stringify(data),
    // credentials: 'include',
  })
    .then((res) => {
      return res.status <= 400 ? res : Promise.reject(res);
    })
    .then((res) => res.json())
    .then((res) => {
      return res;
    })
    .catch((err) => console.log(err));
};

export const updateBoard = (updateBoardData: BoardResponse): Promise<BoardResponse> => {
  console.log('trying to save with:', updateBoardData);
  return fetch(`${BASE_URL}/board/${updateBoardData.boardId}`, {
    method: 'PUT',
    headers: { 'Content-Type': 'application/json' },
    body: JSON.stringify(updateBoardData),
    // credentials: 'include',
  })
    .then((res) => {
      return res.status <= 400 ? res : Promise.reject(res);
    })
    .then((res) => res.json())
    .then((res) => {
      console.log(res);
      return res;
    })
    .catch((err) => console.log(err));
};

