interface TokenResponse {
  boardIds: string[];
  code: number;
  game_version: string;
  language: string;
}

interface TokenUpdateData {
  token: number;
  rooms: Room[];
}

export const updateToken = (tokenUpdateData: TokenUpdateData): Promise<TokenResponse> => {
  console.log('updating token with ', tokenUpdateData);
  return fetch(`${BASE_URL}/token/${tokenUpdateData.token}`, {
    method: 'PUT',
    headers: { 'Content-Type': 'application/json' },
    // credentials: 'include',
    body: JSON.stringify(tokenUpdateData),
  })
    .then((res) => (res.status <= 400 ? res : Promise.reject(res)))
    .then((res) => res.json())
    .then((res) => {
      console.log(res);
      return res;
    })
    .catch((err) => console.log(err));
};

