import React, { FunctionComponent, Dispatch } from 'react';
import { getCardFromId } from '../Utils/Helpers';
import './InitiativeCards.scss';
import { SpecialAction, User } from '../../Interfaces/Board.types';
import { faCertificate } from '@fortawesome/free-solid-svg-icons';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';

interface Props {
  key: number;
  cardId: number;
  again: boolean;
  user: User;
  dispatchSpecials: Dispatch<SpecialAction>;
  setSelectedCard(id: number): void;
}

const InitiativeCard: FunctionComponent<Props> = ({
  cardId,
  setSelectedCard,
  dispatchSpecials,
  again,
  user,
}: Props) => {
  const card = getCardFromId(cardId);

  const onClickHandler = (): void => {
    setSelectedCard(cardId);
    again && dispatchSpecials({ type: 'AGAIN_ID', id: cardId });
  };
  return (
    <>
      <div onClick={onClickHandler} className={`initiative-card-container card${card ? card.id : null}`}>
        {card ? (
          card.effects === 'special' ? (
            <div className="special-card-label small">
              <span className="fa-layers fa-fw">
                <FontAwesomeIcon icon={faCertificate} size="lg" />
                <span className="fa-layers-text">Special</span>
              </span>
            </div>
          ) : null
        ) : null}

        <div className="initiative-card-name">
          {user.language === 'en' ? (
            <p>{card && card.name}</p>
          ) : user.language === 'nl' ? (
            <p>{card && card.name_nl}</p>
          ) : null}
        </div>
        <img className="initiative-card-icon" src={card && card.image} alt="icon of the card" />
        <div className="initiative-card-description">
          {card && user.language === 'en'
            ? card.description
            : card && user.language === 'nl'
            ? card.description_nl
            : null}
        </div>
      </div>
    </>
  );
};

export default InitiativeCard;
