import React, { ChangeEvent, Dispatch, FormEvent, FunctionComponent, SetStateAction, useState } from 'react';
import { useHistory } from 'react-router-dom';
import { adminLogin } from '../../services/SocketApi';
import './AdminLogin.scss';

interface LoginProps {
  name: string;
  password: string;
}

interface Props {
  setSocket: Dispatch<SetStateAction<SocketIOClient.Socket>>;
}

const AdminLogin: FunctionComponent<Props> = ({ setSocket }: Props) => {
  const [loginInput, setLoginInput] = useState<LoginProps>({ name: '', password: '' });
  const history = useHistory();

  const changeHandler = (e: ChangeEvent<HTMLInputElement>): void => {
    const field = e.target.name;
    const value = e.target.value;
    setLoginInput({ ...loginInput, [field]: value });
  };

  const submitHandler = (e: FormEvent) => {
    e.preventDefault();

    if (loginInput.name === 'admin' && loginInput.password === '123') {
      const socket = adminLogin();
      setSocket(socket);
      history.push('/admin');
    } else {
      window.alert('Wrong login');
    }
  };

  return (
    <form className="landing-login-container" id="admin-landing-container" onSubmit={submitHandler}>
      <div className="landing-login-name">
        <label htmlFor="name">
          <input
            type="text"
            name="name"
            id="name"
            onChange={changeHandler}
            value={loginInput.name}
            placeholder="Admin username"
            onFocus={() => setLoginInput({ ...loginInput, name: '' })}
          />
        </label>
      </div>
      <div className="landing-login-password">
        <label htmlFor="password">
          <input
            type="password"
            name="password"
            id="password"
            onChange={changeHandler}
            value={loginInput.password}
            placeholder="Password"
            onFocus={() => setLoginInput({ ...loginInput, password: '' })}
          />
        </label>
      </div>
      <div className="landing-login-button">
        <button className="button" type="submit">
          Admin
        </button>
      </div>
    </form>
  );
};

export default AdminLogin;
