import React, { FunctionComponent } from 'react';
import './CardDecksDisplay.scss';

interface Props {
  drawDisruptionCard: () => void;
  displayDiscardedCards: () => void;
  discardedCard: boolean;
  drawInitiativeCard(): void;
}

const CardDecksDisplay: FunctionComponent<Props> = ({
  drawDisruptionCard,
  displayDiscardedCards,
  discardedCard,
  drawInitiativeCard,
}: Props) => {
  return (
    <div className="card-deck-display">
      {discardedCard ? (
        <div className="card-deck-display_initiative-deck deck" onClick={drawInitiativeCard}></div>
      ) : (
        <div className="card-deck-display_initiative-deck deck"></div>
      )}
      <div onClick={drawDisruptionCard} className="card-deck-display_disruption-deck deck"></div>
      <div onClick={displayDiscardedCards} className="card-deck-display_discard-deck deck">
        <p>Discarded cards</p>
      </div>
    </div>
  );
};

export default CardDecksDisplay;
