import React, { ChangeEvent, FormEvent, FunctionComponent, useState } from 'react';
import { useHistory } from 'react-router-dom';
import { checkToken } from '../../services/ServerApi';
interface Startinput {
  name: string;
  startCode: number;
}

interface Props {
  initSocket: (name: string) => void;
}

const Startcomponent: FunctionComponent<Props> = ({ initSocket }: Props) => {
  const [startInput, setStartInput] = useState<Startinput>({ name: 'Name', startCode: 3005 });
  const history = useHistory();

  const changeHandler = (e: ChangeEvent<HTMLInputElement>): void => {
    const field = e.target.name;
    const value = e.target.value;
    setStartInput({ ...startInput, [field]: value });
  };

  const submitLogin = async (e: FormEvent) => {
    e.preventDefault();
    const auth = await checkToken(startInput.startCode);
    if (auth || startInput.startCode === 3005) {
      initSocket(startInput.name);
      history.push('/lobby');
    } else {
      window.alert('Wrong Startcode');
    }
  };

  return (
    <>
      <h1>Let us start</h1>

      <form id="start-form" onSubmit={submitLogin}>
        <label htmlFor="name">
          <input type="text" name="name" id="name" onChange={changeHandler} value={startInput.name} />
        </label>
        <label htmlFor="startCode ">
          <input type="text" name="startCode" id="startCode" onChange={changeHandler} value={startInput.startCode} />
        </label>
        <button type="submit">Connect</button>
      </form>
    </>
  );
};

export default Startcomponent;
