import React, { ChangeEvent, useEffect, useState } from 'react';
import { emissionEmitter } from '../../services/SocketApi';
import './Admincontrolls.scss';

interface Props {
  gameStarting: boolean;
}

function Admincontrolls({ gameStarting }: Props): JSX.Element {
  const [emissionIntervall, setEmissionIntervall] = useState<number>(5);

  const changeInput = (e: ChangeEvent<HTMLInputElement>) => {
    setEmissionIntervall(parseInt(e.target.value));
  };

  useEffect(() => {
    const interval = setInterval(() => {
      emissionEmitter();
    }, emissionIntervall * 60000);
    return () => clearInterval(interval);
  }, [gameStarting, emissionIntervall]);

  return (
    <div className="admin-controlls-container">
      <form>
        <label htmlFor="range">
          Emission-Intervall in Minutes:
          <br />
          Every {emissionIntervall} Minutes
          <input
            type="range"
            value={emissionIntervall}
            id="range"
            name="range"
            min="1"
            max="10"
            onChange={changeInput}
          />
        </label>
      </form>
    </div>
  );
}

export default Admincontrolls;
