import React, { ChangeEvent, Dispatch, FormEvent, FunctionComponent, SetStateAction, useEffect, useState } from 'react';
import {
  adminCreateRooms,
  sendGameStatus,
  startGame,
  adminUpdatePlayersListener,
  adminUpdateStateListener,
} from '../../services/SocketApi';
import Admincontrolls from '../Admincontrolls/Admincontrolls';
import ChatBox from '../ChatBox/ChatBox';
import Chatmessages from '../Chatmessages/Chatmessages';
import RoomComponent from '../RoomComponent/RoomComponent';
import Spectatecomponent from '../Spectatecomponent/Spectatecomponent';
import { createBoards, createToken, updateToken } from '../../services/ServerApi';
import { Player, Room, Boardsetup, User } from '../../Interfaces/Board.types';
import './Admin.scss';
import '../Chatmessages/Chatmessages.scss';

interface Props {
  setRooms: Dispatch<SetStateAction<Room[]>>;
  rooms: Room[];
  players: Player[];
  setGameStarting: Dispatch<SetStateAction<boolean>>;
  gameStarting: boolean;
  setSocket: Dispatch<SetStateAction<SocketIOClient.Socket>>;
  socket: SocketIOClient.Socket;
  initGame: boolean;
}

interface StateLog {
  [key: string]: Boardsetup;
}

interface Admin {
  name: string;
  designatedRoom: number;
}

interface TokenData {
  language: string;
  game_version: string;
}

const Admin: FunctionComponent<Props> = ({ setRooms, rooms, initGame, gameStarting, socket, players }: Props) => {
  const [numRooms, setnumRooms] = useState<number>(1);
  const [playerLog, setPlayerLog] = useState<Player[]>([]);
  const [stateLog, setStateLog] = useState<StateLog>({});
  const [admin, setAdmin] = useState<User>({ name: 'Admin', designatedRoom: 0, seat: 20, language: 'en' });
  const [tokenData, setTokenData] = useState<TokenData>({ language: 'English', game_version: 'Public' });
  const [token, setToken] = useState<number>(0);
  const [selector, setSelector] = useState<string>('start');
  const [selectedRoom, setSelectedRoom] = useState<number>(0);

  useEffect(() => {
    adminUpdatePlayersListener(setPlayerLog);
    adminUpdateStateListener(setStateLog);
  }, [socket]);

  useEffect(() => {
    if (admin.designatedRoom !== undefined && admin.designatedRoom < numRooms) {
      setSelectedRoom(admin.designatedRoom);
    }
  }, [admin]);

  const createRooms = async (e: FormEvent) => {
    e.preventDefault();

    const roomsFromServer = await createBoards({ roomNumber: numRooms, tokenId: token });

    const parsedRooms = roomsFromServer.map((room: Room) => {
      const newRoom = { ...room, players: [] };
      return newRoom;
    });
    const tokenAnswer = await updateToken({ token: token, rooms: roomsFromServer });
    console.log(tokenAnswer);
    setRooms(() => parsedRooms);
    setSelector('lobby');
  };

  useEffect(() => {
    adminCreateRooms(rooms);
  }, [rooms]);

  const submitGame = () => {
    const status = !gameStarting;
    sendGameStatus(status);
    setTimeout(() => {
      setSelector('game');
      startGame();
    }, 2000);
  };

  const getToken = async (e: FormEvent) => {
    e.preventDefault();
    const newToken = await createToken(tokenData);
    if (newToken) {
      setToken(newToken.code);
    } else {
      setToken(3005);
    }
    setSelector('create');
  };

  return (
    <div className="admin-container">
      <div className="admin-content-container">
        <h1>Welcome Admin</h1>

        {selector === 'start' ? (
          <div className="admin-start-container choices-container">
            <h3>Do you want to create a new game or load an old one?</h3>
            <div className="create-game-buttons">
              <button className="button" onClick={() => setSelector('token')}>
                Create new game
              </button>
              <button className="button" onClick={() => setSelector('load')}>
                Load old game
              </button>
            </div>
          </div>
        ) : null}

        {selector === 'token' ? (
          <div className="admin-tokenselector-container choices-container">
            <form onSubmit={getToken} className="admin-language-form">
              <div id="admin-token-labels">
                <label htmlFor="version">Version</label>
                <select
                  defaultValue="Public"
                  onChange={(e) => setTokenData({ ...tokenData, game_version: e.target.value })}
                >
                  <option value="Public">Public</option>
                  <option value="Organisation">Organisation</option>
                </select>
              </div>
              <button className="button" type="submit">
                Get Startcode
              </button>
            </form>
          </div>
        ) : null}

        {token > 0 && selector !== 'create' ? (
          <div className="token-container">
            Give this code to your players: <span id="admin-token">{token}</span>
          </div>
        ) : null}

        {selector === 'create' ? (
          <div className="admin-room-form choices-container">
            <form onSubmit={createRooms}>
              <label htmlFor="room-numbers"></label>
              <h3>How many rooms?</h3>
              <input
                name="room-numbers"
                id="room-numbers"
                type="number"
                min="1"
                max="30"
                onChange={(e) => setnumRooms(parseInt(e.target.value))}
                value={numRooms}
              />
              <button className="button" type="submit">
                Submit
              </button>
            </form>
          </div>
        ) : null}

        {selector === 'lobby' || selector === 'game' ? (
          <>
            <Admincontrolls gameStarting={gameStarting} />
          </>
        ) : null}

        {selector === 'lobby' || selector === 'game' ? (
          gameStarting === false ? (
            <button className="button start-game-button" onClick={submitGame}>
              {' '}
              Start Game{' '}
            </button>
          ) : (
            <button className="button start-game-button" onClick={submitGame}>
              {' '}
              End Game{' '}
            </button>
          )
        ) : null}

        {selector === 'lobby' || selector === 'game' ? (
          <div className="admin-room-overview">
            {initGame === false ? (
              <div className="admin-rooms-container">
                <RoomComponent
                  room={rooms[selectedRoom]}
                  joinRoom={() => setAdmin({ ...admin, designatedRoom: selectedRoom })}
                  user={admin}
                  leaveRoom={() => setAdmin({ ...admin, designatedRoom: 0 })}
                  gameStarting={gameStarting}
                />
              </div>
            ) : (
              <div className="admin-rooms-container ">
                <Spectatecomponent socket={socket} />
              </div>
            )}
            <div className="admin-messages-container">
              <Chatmessages channel={99} />
              <ChatBox user={admin} channel={admin.designatedRoom} />
            </div>
            <div className="admin-room-buttons-text">Navigate between rooms:</div>
            <div className="admin-room-buttons">
              {rooms.map((room) => (
                <button
                  key={`room-key-${room.id}`}
                  onClick={() => setAdmin({ ...admin, designatedRoom: room.id })}
                  className="button"
                >
                  {room.name}
                </button>
              ))}
              <button className="button" onClick={() => setAdmin({ ...admin, designatedRoom: 99 })}>
                All
              </button>
            </div>
          </div>
        ) : null}
      </div>
    </div>
  );
};

export default Admin;
