import React, { FunctionComponent, useEffect } from 'react';
import './Tutorial.scss';

interface P {
  tutorialHand: () => void;
  tutorialHighlighter: (className: string) => void;
}

const Initiative: FunctionComponent<P> = ({ tutorialHand, tutorialHighlighter }: P): JSX.Element => {
  useEffect(() => {
    tutorialHand();
    tutorialHighlighter('.zillian');
  }, []);

  return (
    <>
      <div className="dialog-box dialog-box-zillian">
        <div>
          <p className="minion-message">
            Every player has 3 cards on their hands. Since this is a cooperative game, you can see your team members
            cards at any time by clicking their avatar on the left.
            <br />
            <br />
            If you play an Initiative Card, read it to your players, some might require some discussion.
          </p>
        </div>
      </div>
      <div className="dialog-box-pointer"></div>
    </>
  );
};

export default Initiative;
