import React from 'react';
import './Annoucement.scss';

interface Props {
  announcement: string;
}

function Announcement({ announcement }: Props): JSX.Element {
  return (
    <>
      {announcement !== '' ? (
        <div className="announcement-bubble">
          <div className="announcement-dialog-box-climmie">{announcement}</div>
        </div>
      ) : null}
    </>
  );
}

export default Announcement;
