import React, { FunctionComponent, useEffect } from 'react';
import './Tutorial.scss';

interface P {
  tutorialHighlighter: (className: string) => void;
}

const Shields: FunctionComponent<P> = ({ tutorialHighlighter }: P): JSX.Element => {
  useEffect(() => {
    tutorialHighlighter('.shield');
    tutorialHighlighter('.victory-track');
    tutorialHighlighter('.zillian');
  }, []);

  return (
    <>
      <div className="dialog-box dialog-box-zillian">
        <div>
          <p className="minion-message">
            Every Pathway starts with a Shield. The shield can protect your team. Once a shield has 3 Zillians, its
            token moves to the win path.
            <br />
            <br />
            The goal of the game is to complete the win path with 5 pathway tokens.
          </p>
        </div>
      </div>
      <div className="dialog-box-pointer"></div>
    </>
  );
};

export default Shields;
