import React, { FunctionComponent } from 'react';
import { IDisruptionCard, User } from '../../Interfaces/Board.types';
import './DisruptionCard.scss';

interface Props {
  crystal?: boolean;
  card: IDisruptionCard;
  number?: number;
  selector?: (id: number) => void;
  selection?: number[];
  user: User;
}

const DisruptionCard: FunctionComponent<Props> = ({ number, card, crystal, selector, selection, user }: Props) => {
  const clickHandler = (): void => {
    if (selector && number !== undefined) selector(number);
  };
  return (
    <>
      <div onClick={clickHandler} className={`disruption-card-container`}>
        <div className="disruption-card-name">
          {user.language === 'en' ? (
            <p>{card && card.name}</p>
          ) : user.language === 'nl' ? (
            <p>{card && card.name_nl}</p>
          ) : null}
        </div>
        <img className="disruption-card-icon" src={card && card.image} alt="icon of the card" />
        <div className="disruption-card-description">
          {card && user.language === 'en'
            ? card.description
            : card && user.language === 'nl'
            ? card.description_nl
            : null}
        </div>
        {crystal && selection && number !== undefined && selection[number] !== 5 ? (
          <div className="crystal">
            <p className="crystal-number">{selection[number]}</p>
          </div>
        ) : null}
      </div>
    </>
  );
};

export default DisruptionCard;
