import { faCertificate } from '@fortawesome/free-solid-svg-icons';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import React, { FunctionComponent } from 'react';
import { getCardFromId } from '../Utils/Helpers';
import './InitiativeCards.scss';

interface Props {
  key: number;
  cardId: number;
}

const InitiativeCardWithoutFunctionality: FunctionComponent<Props> = ({ cardId }: Props) => {
  const card = getCardFromId(cardId);

  return (
    <>
      <div className={`initiative-card-container card${card ? card.id : null}`}>
        <div className="initiative-card-name">
          <p>{card && card.name}</p>
        </div>
        <img className="initiative-card-icon" src={card && card.image} alt="icon of the card" />
        <div className="initiative-card-description">{card && card.description}</div>
      </div>
    </>
  );
};

export default InitiativeCardWithoutFunctionality;
