import React, { useState, useEffect } from 'react';
import { Player, Boardsetup } from '../../Interfaces/Board.types';
import { adminUpdatePlayersListener, adminUpdateStateListener } from '../../services/SocketApi';
import './Spectatecomponent.scss';

interface Props {
  socket: SocketIOClient.Socket;
}

interface StateLog {
  [key: string]: Boardsetup;
}

function Spectatecomponent({ socket }: Props): JSX.Element {
  const [playerLog, setPlayerLog] = useState<Player[]>([]);
  const [stateLog, setStateLog] = useState<StateLog>({});

  useEffect(() => {
    adminUpdatePlayersListener(setPlayerLog);
    adminUpdateStateListener(setStateLog);
  }, [socket]);

  // useEffect(() => {
  //   setPlayerLog();
  // })

  return (
    <div className="spectate-container">
      <div className="player-log">
        {playerLog.map((player, i) => (
          <div key={`${player.name}${i}`} className="player">
            Time: {Date.now()}: Name - {player.name}, room - {player.designatedRoom}, position - {player.position}
          </div>
        ))}
      </div>

      <div className="room-states">
        {Object.keys(stateLog).map((key) => (
          <div key={key} className="state">
            <h2 className="room-number">Room: {key}</h2>
            <div className="state-status">
              <p>Active player: {stateLog[key].activeSeat}</p>
              <p>Carbions: {stateLog[key].carbions}</p>
              <p>Climmies: {stateLog[key].climmies}</p>
              <p>Coins: {stateLog[key].coins}</p>
            </div>
          </div>
        ))}
      </div>

      <div className="player-log">
        {playerLog.map((player, i) => (
          <div key={`${player.name}${i}`} className="player">
            Time: {Date.now()}: Name - {player.name}, room - {player.designatedRoom}, position - {player.position}
          </div>
        ))}
      </div>
    </div>
  );
}

export default Spectatecomponent;
