import React from 'react';
import { FunctionComponent } from 'react';
import { Player, User } from '../../Interfaces/Board.types';
import InitiativeCardWithoutFunctionality from '../InitiativeCards/InitiativeCardsWithoutFunctionality';
import RoleCards from '../RoleCards/';
import './OtherPlayersCardsDisplay.scss';

interface Props {
  player: Player | undefined;
  setOtherPlayer(player: Player | undefined): void;
  user: User;
}

const OtherPlayersCardsDisplay: FunctionComponent<Props> = ({ setOtherPlayer, player, user }: Props) => {
  const hideOtherPlayersCards = (): void => {
    setOtherPlayer(undefined);
  };

  return (
    <>
      <div className="selected-cards-grey-out display-off"></div>
      <div onClick={hideOtherPlayersCards} className="selected-cards display-off">
        <div className="role-card">{player && player.role ? <RoleCards user={user} role={player.role} /> : null}</div>
        {player && player.seat !== user.seat
          ? player.hand.map((cardId, i) => {
              return (
                <div className={`selected-cards-display-item other-players-card${i + 1}`} key={cardId}>
                  <InitiativeCardWithoutFunctionality key={cardId} cardId={cardId} />
                </div>
              );
            })
          : null}
      </div>
    </>
  );
};

export default OtherPlayersCardsDisplay;
