import React, { FunctionComponent, useEffect } from 'react';
import './Tutorial.scss';

interface P {
  tutorialHighlighter: (className: string) => void;
}

const Zillians: FunctionComponent<P> = ({ tutorialHighlighter }: P): JSX.Element => {
  useEffect(() => {
    tutorialHighlighter('.zillian');
    tutorialHighlighter('.shield');
  }, []);

  return (
    <>
      <div className="dialog-box dialog-box-zillian">
        <div>
          <p className="minion-message">
            A pathway is protected once you place 3 Zillians on the shield.
            <br />
            Climmies may still be added to the protected pathway due to disruptions. However, removal is easier for
            protected pathways: they will be removed automatically for no cost when you move over them.
            <br />
            Additional zillians may still be added to the pathway, and ultimately to your final score.
          </p>
        </div>
      </div>
      <div className="dialog-box-pointer"></div>
    </>
  );
};

export default Zillians;
