import React, { FunctionComponent, useEffect } from 'react';
import './Tutorial.scss';
import RoleCard from '../RoleCards/index';
import { Role, User } from '../../Interfaces/Board.types';

interface P {
  tutorialHighlighter: (className: string) => void;
  myRole: Role;
  user: User;
}

const Roles: FunctionComponent<P> = ({ tutorialHighlighter, myRole, user }: P): JSX.Element => {
  useEffect(() => {
    tutorialHighlighter('.player');
    tutorialHighlighter('.start');
    tutorialHighlighter('.start');
    tutorialHighlighter('.zillian');
  }, []);

  return (
    <>
      <div className="tut-role-card">{myRole ? <RoleCard user={user} role={myRole} /> : null}</div>
      <div className="dialog-box dialog-box-zillian">
        <div>
          <p className="minion-message">
            Each player has a different role. You are invited to introduce your role to your team. Make use of your
            special powers!
            <br />
            <br />
            All players start with pawns of corresponding color on starting position.
            <br />
            <br />
            Pssst.. if you forgot your special power, click your player avatar on the left.
          </p>
        </div>
      </div>
      <div className="dialog-box-pointer"></div>
    </>
  );
};

export default Roles;
