import React, { ChangeEvent, FormEvent, FunctionComponent, useState } from 'react';
import { useHistory } from 'react-router-dom';
import { checkToken } from '../../services/ServerApi';
import './UserLogin.scss';

interface LoginProps {
  name: string;
  password: string;
}

interface Props {
  initSocket: (name: string) => void;
}

const UserLogin: FunctionComponent<Props> = ({ initSocket }: Props) => {
  const [loginInput, setLoginInput] = useState<LoginProps>({ name: '', password: '' });
  const history = useHistory();

  const changeHandler = (e: ChangeEvent<HTMLInputElement>): void => {
    const field = e.target.name;
    const value = e.target.value;
    setLoginInput({ ...loginInput, [field]: value });
  };

  const submitLogin = async (e: FormEvent) => {
    e.preventDefault();
    const auth = await checkToken(parseInt(loginInput.password));
    if (auth || loginInput.password === '3005') {
      initSocket(loginInput.name);
      history.push('/lobby');
    } else {
      window.alert('Wrong Startcode');
    }
  };

  return (
    <form className="landing-login-container" id="user-landing-container" onSubmit={submitLogin}>
      <div className="landing-login-name">
        <label htmlFor="name">
          <input
            type="text"
            name="name"
            id="name"
            onChange={changeHandler}
            value={loginInput.name}
            placeholder="Name"
            onFocus={() => setLoginInput({ ...loginInput, name: '' })}
          />
        </label>
      </div>
      <div className="landing-login-password">
        <label htmlFor="password ">
          <input
            type="text"
            name="password"
            id="password"
            onChange={changeHandler}
            value={loginInput.password}
            placeholder="Enter room code..."
            onFocus={() => setLoginInput({ ...loginInput, password: '' })}
          />
        </label>
      </div>
      <div className="landing-login-button">
        <button className="button" type="submit">
          Join lobby
        </button>
      </div>
    </form>
  );
};

export default UserLogin;
