import React, { Dispatch, FunctionComponent, SetStateAction, useEffect } from 'react';
import './PlayerDisplay.scss';
import { Player, User, Room } from '../../Interfaces/Board.types';
import defaultPlayer from '../../assets/roles/undefined.svg';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faVolumeMute, faVolumeUp, faQuestion } from '@fortawesome/free-solid-svg-icons';
import { faTelegramPlane } from '@fortawesome/free-brands-svg-icons';

interface Props {
  score: number;
  players: Player[];
  user: User;
  setOtherPlayer(player: Player | undefined): void;
  rooms: Room[];
  muteHandler(): void;
  isMute: boolean;
  setReferenceDisplay: Dispatch<SetStateAction<boolean>>;
  referenceDisplay: boolean;
  tutorialRole: boolean;
  activeSeat: number;
}

const PlayerDisplay: FunctionComponent<Props> = ({
  user,
  score,
  players,
  setOtherPlayer,
  rooms,
  muteHandler,
  tutorialRole,
  activeSeat,
  isMute,
  setReferenceDisplay,
  referenceDisplay,
}: Props) => {
  const room = players[0].designatedRoom;
  const tableName = rooms.find((el) => el.id === room)?.name;

  useEffect(() => {
    const currentPlayer = players.find((player) => player.seat === activeSeat);
    const highlightedPlayer = document.querySelector('.current-player-highlight');
    if (highlightedPlayer) {
      highlightedPlayer.classList.remove('current-player-highlight');
    }
    if (currentPlayer !== undefined) {
      const curPlayer = document.querySelector(`.player-seat-${currentPlayer.seat}`);
      curPlayer ? curPlayer.classList.add('current-player-highlight') : null;
    }
  }, [activeSeat]);

  const generatePlayerDivs = players.map((player) => {
    let classes = `player-display-frame player-seat-${player.seat}`;
    if (player.seat === user.seat) {
      classes = `player-display-frame player-seat-${player.seat} user player-role-${player.role?.name}`;
    }
    return (
      <div onClick={() => setOtherPlayer(player)} key={player.name} className={classes}>
        <img
          className="player-display-frame_img"
          src={player.role?.name !== 'default' && tutorialRole ? player.role?.image : defaultPlayer}
          alt="player image"
        ></img>
        <div className="player-display-frame_stats">
          <h5>{player.name}</h5>
          <p>actions left: {player.remainingActions}</p>
        </div>
      </div>
    );
  });

  return (
    <>
      <div className="table-name-and-score">
        <h2>
          <div>
            Team <br />
            {tableName}
          </div>
          <div>Score: {score}</div>
        </h2>
      </div>
      <div className="player-display">{generatePlayerDivs}</div>
      <div className="player-display-icons">
        <div className="icon-mute display-icon">
          {isMute ? (
            <FontAwesomeIcon icon={faVolumeMute} onClick={muteHandler} />
          ) : (
            <FontAwesomeIcon icon={faVolumeUp} onClick={muteHandler} />
          )}
        </div>
        <div className="icon-reference display-icon">
          <FontAwesomeIcon icon={faQuestion} onClick={() => setReferenceDisplay(!referenceDisplay)} />
        </div>
        <div className="icon-chat display-icon">
          <FontAwesomeIcon icon={faTelegramPlane} />
        </div>
      </div>
    </>
  );
};

export default PlayerDisplay;
