import React, { FunctionComponent } from 'react';
import Playercomponent from '../Player/Playercomponent';
import { User, Room } from '../../Interfaces/Board.types';
import './RoomComponent.scss';

export interface Props {
  user: User;
  room: Room;
  joinRoom: (roomId: number) => void;
  leaveRoom: (roomId: number) => void;
  gameStarting: boolean;
}

const RoomComponent: FunctionComponent<Props> = ({ room, joinRoom, leaveRoom, user, gameStarting }: Props) => {
  return (
    <div className="room-container">
      <div className="room-container-content">
        <div id="room-title">Team {room.name}</div>
        <div className="room-players-container">
          {room.players.map((player) => {
            return <Playercomponent key={player.name} name={player.name} />;
          })}
        </div>

        {gameStarting === false ? (
          room.players.length === 4 ? (
            room.players.findIndex((player) => {
              return user.name === player.name;
            }) === -1 ? (
              <button className="button">Full</button>
            ) : (
              <button className="button" id="button-sml" onClick={() => leaveRoom(room.id)}>
                Leave
              </button>
            )
          ) : user.designatedRoom === room.id ? (
            <button className="button" onClick={() => leaveRoom(room.id)}>
              Leave
            </button>
          ) : (
            <button className="button" onClick={() => joinRoom(room.id)}>
              Join
            </button>
          )
        ) : (
          <button className="button">Game starts</button>
        )}
      </div>
    </div>
  );
};

export default RoomComponent;
