import { Player, PlayerAction } from '../Interfaces/Board.types';

export const playersReducer = (players: Player[], playerAction: PlayerAction): Player[] => {
  switch (playerAction.type) {
    case 'REFRESH_ACTIONS':
      const newArray = players.map((player) => {
        if (playerAction.current && playerAction.actions && player.seat === playerAction.current.seat) {
          if (playerAction.actions === 4) player.remainingActions = 4;
          else if (player.remainingActions >= 1) player.remainingActions += playerAction.actions;
        }
        return player;
      });
      return newArray;
    case 'UPDATE_PLAYERS':
      return playerAction.playersArray ? playerAction.playersArray : players;
    default:
      throw new Error('Something went wrong in player reducer');
  }
};
