import React, { Dispatch } from 'react';
import { FunctionComponent } from 'react';
import { SpecialAction, User } from '../../Interfaces/Board.types';
import DisruptionCard from '../DisruptionCards';
import InitiativeCard from '../InitiativeCards';
import { getCardFromId, isDisruptionCard, isInitiativeCard } from '../Utils/Helpers';
import './DiscardedCardsDisplay.scss';

interface Props {
  discardedDisruptionCards: number[];
  discardedInitiativeCards: number[];
  setSelectedCard(id: number): void;
  dispatchSpecials: Dispatch<SpecialAction>;
  again: boolean;
  hideDiscardedCards(): void;
  user: User;
}

const DiscardedCardsDisplay: FunctionComponent<Props> = ({
  discardedDisruptionCards,
  discardedInitiativeCards,
  again,
  setSelectedCard,
  dispatchSpecials,
  hideDiscardedCards,
  user,
}: Props) => {
  const initiativeCardsContainer = () => {
    return discardedInitiativeCards.map((cardId) => {
      const card = getCardFromId(cardId);
      return card && isInitiativeCard(card) ? (
        <div key={cardId} className="discarded-initiative-card">
          <InitiativeCard
            user={user}
            key={card.id}
            cardId={cardId}
            dispatchSpecials={dispatchSpecials}
            again={again}
            setSelectedCard={setSelectedCard}
          />
        </div>
      ) : null;
    });
  };

  const disruptionCardsContainer = () => {
    return discardedDisruptionCards.map((cardId) => {
      const card = getCardFromId(cardId);

      return card && isDisruptionCard(card) ? (
        <div key={cardId} className="discarded-initiative-card">
          <DisruptionCard card={card} user={user} />
        </div>
      ) : null;
    });
  };
  return (
    <>
      <div onClick={hideDiscardedCards} className="discarded-display-grey-out display-off"></div>
      <div onClick={hideDiscardedCards} className="discarded-display display-off">
        <h3 className="init-title discarded-text">Initiative Cards:</h3>
        <div className="discarded-initiative-cards dis">{initiativeCardsContainer()}</div>
        <h3 className="dis-title discarded-text">Disruption Cards:</h3>
        <div className="discarded-disruption-cards dis">{disruptionCardsContainer()}</div>
      </div>
    </>
  );
};

export default DiscardedCardsDisplay;
