import React, { FunctionComponent } from 'react';
import './RoleCards.scss';
import { Role, User } from '../../Interfaces/Board.types';

interface Props {
  role: Role;
  user: User;
}

const RoleCards: FunctionComponent<Props> = ({ role, user }: Props) => {
  return (
    <>
      <div className="role-card-container">
        <div className="role-name">
          <h3>{role.name}</h3>
        </div>
        <img className="role-icon" src={role.image} alt="image of the player role" />
        <div className="role-description role-text">
          {user.language === 'en' ? (
            <p>{role.description}</p>
          ) : user.language === 'nl' ? (
            <p>{role.description_nl}</p>
          ) : null}
        </div>
        <h5 className="power-label">Special Power</h5>
        <div className="role-text role-power">
          {user.language === 'en' ? <p>{role.power}</p> : user.language === 'nl' ? <p>{role.power_nl}</p> : null}
        </div>
      </div>
    </>
  );
};

export default RoleCards;
