import React, { FunctionComponent, useEffect } from 'react';
import './Tutorial.scss';

interface P {
  tutorialHighlighter: (className: string) => void;
}

const Bailout: FunctionComponent<P> = ({ tutorialHighlighter }: P): JSX.Element => {
  useEffect(() => {
    tutorialHighlighter('.climmie');
    tutorialHighlighter('.core');
  }, []);

  return (
    <>
      <div className="dialog-box dialog-box-climmie">
        <div>
          <p className="minion-message">
            If three of our climmies reach your core, you will need a bailout from your governments and financial
            institutions to get back on your feet.
            <br />
            <br />
            Every time that happens, these climmies will be removed from the game and 3 points will be deducted from
            your final score.
          </p>
        </div>
      </div>
      <div className="dialog-box-pointer-climmie"></div>
    </>
  );
};

export default Bailout;
