import React, { Dispatch, FunctionComponent, SetStateAction, useEffect } from 'react';
import { useHistory } from 'react-router-dom';
import { checkSession, loadBoard } from '../../services/ServerApi';
import AdminLogin from '../AdminLogin/AdminLogin';
import UserLogin from '../UserLogin/UserLogin';
import './Landing.scss';

interface Props {
  setSocket: Dispatch<SetStateAction<SocketIOClient.Socket>>;
  initSocket: (name: string) => void;
}

const Landing: FunctionComponent<Props> = ({ setSocket, initSocket }: Props) => {
  const history = useHistory();

  useEffect(() => {
    const fetchSession = async () => {
      const auth = await checkSession();
      if (!auth) {
        history.push('/');
      } else {
        if (auth.loggedIn && auth.gameId) {
          const board = loadBoard(auth.gameId);
          console.log(board);
          history.push('/admin-login');
        } else if (auth.loggedIn) {
          history.push('/lobby');
        }
      }
    };
    fetchSession();
  }, []);
  return (
    <div className="landing-container">
      <div className="landing-container-content">
        <div className="admin-login login">
          <AdminLogin setSocket={setSocket} />
        </div>
        <div className="user-login login">
          <UserLogin initSocket={initSocket} />
        </div>
      </div>
    </div>
  );
};

export default Landing;
